import * as Dialog from '@radix-ui/react-dialog';
import * as Select from '@radix-ui/react-select'
import { useState } from 'react';
import { CheckIcon, ChevronDownIcon, ChevronUpIcon } from '@radix-ui/react-icons'
import config from '../../../config';

const matchesSelectEnuem = {
    premier: "Premere",
    competitive: "Competitive",
    wingman: "Wingman"
}

const cheatsSelectEnuem = {
    gamesense: "GameSense",
    neverlose: "Neverlose",
    fatality: "Fatality",
    aimware: "Aimware",
    obstruction: "Obstruction",
    other: "Other"
}

const lobbiesEnuem = {
    any: "Any",
    legit: "Legit",
    semi: "Semi-Rage",
    rage: "Rage / HvH",
    grief: "Griefing"
}

const AddCodeDialogue = ({setCodesList}) => {
    const [matche, setMatche] = useState("premier")
    const [cheat, setCheat] = useState("gamesense")
    const [lobby, setLobby] = useState("any")
    const [friendCodeValue, setFriendCodeValue] = useState("")
    const [minElo, setMinElo] = useState("20000")
    const [lookingForGame, setLookingForGame] = useState("20000")
    const [players, setPlayers] = useState("")
    const [error, setError] = useState(null)
    const [modalOpen, setModalOpen] = useState(null)


    const handleFriendCodeChange = e => {
        setFriendCodeValue(e.target.value.trim())
    }

    const handleMinEloChange = e => {
        setMinElo(e.target.value.trim())
    }

    const handlePlayersChange = e => {
        setPlayers(e.target.value.trim())
    }


    const handleMatche = value => {
        if(!value) return
        setMatche(value)
    }

    const handleCheat = value => {
        if(!value) return
        setCheat(value)
    }

    const handleLobby = value => {
        if(!value) return
        setLobby(value)
    }

    const handleFormSubmit = e => {
        e.preventDefault()

        const requestData = {
            match: matche,
            cheat,
            lobby,
            friendCode: friendCodeValue,
            minElo: minElo === "" ? 0 : minElo,
            lookingForGame,
            playersNeeded: players
        }

        fetch( `${config.BASE_URL}/api/matchmaking`, {
            headers: {
                "Content-Type": "application/json",
            },
            method: "POST",
            body: JSON.stringify(requestData),
        } )
            .then( res => res.json() )
            .then( res => {
                if( res.error ) {
                    setError(res.error)
                    return
                }
                setCodesList( oldCodesList => {
                    return [
                        ...oldCodesList,
                        res
                    ]
                } )
                setModalOpen(false)
            } )
            .catch( err => {
                console.error('error fetching matchmaking data:', err.message)
            } )


    }


    

    return <>
        <Dialog.Root open={modalOpen}>
            <Dialog.Trigger asChild={true}>
                <button onClick={() => setModalOpen(true)} className="inline-flex items-center justify-center whitespace-nowrap rounded-md text-sm font-medium ring-offset-background transition-colors focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring focus-visible:ring-offset-2 disabled:pointer-events-none disabled:opacity-50 bg-primary text-primary-foreground hover:bg-primary/90 h-10 px-4 py-2 space-x-1" type="button" aria-haspopup="dialog" aria-expanded="false" aria-controls="radix-:ron:" data-state="closed">
                    <svg stroke="currentColor" fill="none" strokeWidth={2} viewBox="0 0 24 24" strokeLinecap="round" strokeLinejoin="round" height="1em" width="1em" xmlns="http://www.w3.org/2000/svg">
                        <path d="M5 12h14" />
                        <path d="M12 5v14" />
                    </svg>
                    <span>Add Code</span>
                </button>
            </Dialog.Trigger>
            <Dialog.Portal>
                <Dialog.Overlay onClick={() => setModalOpen(false)} className="fixed inset-0 z-50 bg-black/80 data-[state=open]:animate-in data-[state=closed]:animate-out data-[state=closed]:fade-out-0 data-[state=open]:fade-in-0" />
                <Dialog.Content className="fixed left-[50%] top-[50%] z-50 grid w-full max-w-lg translate-x-[-50%] translate-y-[-50%] gap-4 border bg-background p-6 shadow-lg duration-200 data-[state=open]:animate-in data-[state=closed]:animate-out data-[state=closed]:fade-out-0 data-[state=open]:fade-in-0 data-[state=closed]:zoom-out-95 data-[state=open]:zoom-in-95 data-[state=closed]:slide-out-to-left-1/2 data-[state=closed]:slide-out-to-top-[48%] data-[state=open]:slide-in-from-left-1/2 data-[state=open]:slide-in-from-top-[48%] sm:rounded-lg md:max-w-screen-sm">
                    <div className="flex flex-col space-y-1.5 text-center sm:text-left">
                        <Dialog.Title className="text-lg font-semibold leading-none tracking-tight" >
                            Add Friend Code
                        </Dialog.Title>
                        <Dialog.Description className="text-sm text-muted-foreground" >
                            Share your friend code to matchmake with other players
                        </Dialog.Description>
                    </div>

                    {
                        error !== null ? (
                            <div role="alert" className="relative w-full rounded-lg border p-4 [&>svg~*]:pl-7 [&>svg+div]:translate-y-[-3px] [&>svg]:absolute [&>svg]:left-4 [&>svg]:top-4 bg-destructive/50 border-destructive text-destructive-foreground dark:border-destructive [&>svg]:text-destructive-foreground">
                                {error}
                            </div>   
                        ) : (false)
                    }


                    <form onSubmit={handleFormSubmit} className="flex flex-col gap-8 mt-4">
                        <div className="grid grid-cols-1 sm:grid-cols-2 gap-4">
                            <div className="space-y-2">
                                <label className="text-sm font-medium leading-none peer-disabled:cursor-not-allowed peer-disabled:opacity-70" htmlFor=":r2b:-form-item">Match type</label>

                                <Select.Root value={matche} onValueChange={handleMatche} >
                                    <Select.Trigger className="flex h-10 w-full items-center justify-between rounded-md border border-input bg-background px-3 py-2 text-sm ring-offset-background placeholder:text-muted-foreground focus:outline-none focus:ring-2 focus:ring-ring focus:ring-offset-2 disabled:cursor-not-allowed disabled:opacity-50 [&>span]:line-clamp-1">
                                        <Select.Value>
                                            {
                                                matchesSelectEnuem[matche]
                                            }
                                        </Select.Value>
                                        <Select.Icon className="lucide lucide-chevron-down h-4 w-4 opacity-50">
                                            <ChevronDownIcon />
                                        </Select.Icon>
                                    </Select.Trigger>
                                    <Select.Portal>
                                        <Select.Content position="popper" side="bottom" className="relative z-50 max-h-96 min-w-[8rem] overflow-hidden rounded-md border bg-popover/75 backdrop-blur-md text-popover-foreground shadow-md data-[state=open]:animate-in data-[state=closed]:animate-out data-[state=closed]:fade-out-0 data-[state=open]:fade-in-0 data-[state=closed]:zoom-out-95 data-[state=open]:zoom-in-95 data-[side=bottom]:slide-in-from-top-2 data-[side=left]:slide-in-from-right-2 data-[side=right]:slide-in-from-left-2 data-[side=top]:slide-in-from-bottom-2 data-[side=bottom]:translate-y-1 data-[side=left]:-translate-x-1 data-[side=right]:translate-x-1 data-[side=top]:-translate-y-1">
                                            <Select.Viewport className="p-1 h-[var(--radix-select-trigger-height)] w-full min-w-[var(--radix-select-trigger-width)]">
                                                <Select.Group>
                                                    <Select.Item value="premier" className="relative flex w-full cursor-default select-none items-center rounded-sm py-1.5 pl-8 pr-2 text-sm outline-none focus:bg-accent focus:text-accent-foreground data-[disabled]:pointer-events-none data-[disabled]:opacity-50">
                                                        {matche === "premier" && <SelectedCheckSVG/>}
                                                        Premier
                                                    </Select.Item>
                                                    <Select.Item value="competitive" className="relative flex w-full cursor-default select-none items-center rounded-sm py-1.5 pl-8 pr-2 text-sm outline-none focus:bg-accent focus:text-accent-foreground data-[disabled]:pointer-events-none data-[disabled]:opacity-50">
                                                        {matche === "competitive" && <SelectedCheckSVG/>}
                                                        Competitive
                                                    </Select.Item>
                                                    <Select.Item value="wingman" className="relative flex w-full cursor-default select-none items-center rounded-sm py-1.5 pl-8 pr-2 text-sm outline-none focus:bg-accent focus:text-accent-foreground data-[disabled]:pointer-events-none data-[disabled]:opacity-50">
                                                        {matche === "wingman" && <SelectedCheckSVG/>}
                                                        Wingman
                                                    </Select.Item>
                                                </Select.Group>
                                            </Select.Viewport>

                                        </Select.Content>
                                    </Select.Portal>
                                </Select.Root>

                                <p id=":r2b:-form-item-description" className="text-sm text-muted-foreground">The type of match that you want to play.</p>
                            </div>
                            <div className="space-y-2">
                                <label className="text-sm font-medium leading-none peer-disabled:cursor-not-allowed peer-disabled:opacity-70" htmlFor=":r2d:-form-item">Friend code</label>
                                <input value={friendCodeValue} onChange={handleFriendCodeChange} className="flex h-10 rounded-md border border-input bg-background px-3 py-2 text-sm ring-offset-background file:border-0 file:bg-transparent file:text-sm file:font-medium placeholder:text-muted-foreground focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring focus-visible:ring-offset-2 disabled:cursor-not-allowed disabled:opacity-50 w-full" placeholder="Friend code" autoComplete="off" data-lpignore="true" data-form-type="other" id=":r2d:-form-item" aria-describedby=":r2d:-form-item-description" aria-invalid="false" type="text" name="code" />
                                <p id=":r2d:-form-item-description" className="text-sm text-muted-foreground">Enter your friend code to share with other players.</p>
                            </div>
                        </div>
                        <div className={`grid grid-cols-1 gap-4 ${matche === "premier" ? 'md:grid-cols-2' : ''}`}>
                            <div className="space-y-2">
                                <label className="text-sm font-medium leading-none peer-disabled:cursor-not-allowed peer-disabled:opacity-70" htmlFor=":r2e:-form-item">Cheat</label>

                                <Select.Root value={cheat} onValueChange={handleCheat} >
                                    <Select.Trigger className="flex h-10 w-full items-center justify-between rounded-md border border-input bg-background px-3 py-2 text-sm ring-offset-background placeholder:text-muted-foreground focus:outline-none focus:ring-2 focus:ring-ring focus:ring-offset-2 disabled:cursor-not-allowed disabled:opacity-50 [&>span]:line-clamp-1">
                                        <Select.Value>
                                            <span style={{pointerEvents: 'none'}}>
                                                <div className="w-full flex items-center space-x-2">                                                
                                                    {
                                                        cheat === "other" ? (
                                                            <svg stroke="currentColor" fill="currentColor" strokeWidth={0} viewBox="0 0 320 512" className="w-7 h-7 p-1" height="1em" width="1em" xmlns="http://www.w3.org/2000/svg">
                                                                <path d="M80 160c0-35.3 28.7-64 64-64h32c35.3 0 64 28.7 64 64v3.6c0 21.8-11.1 42.1-29.4 53.8l-42.2 27.1c-25.2 16.2-40.4 44.1-40.4 74V320c0 17.7 14.3 32 32 32s32-14.3 32-32v-1.4c0-8.2 4.2-15.8 11-20.2l42.2-27.1c36.6-23.6 58.8-64.1 58.8-107.7V160c0-70.7-57.3-128-128-128H144C73.3 32 16 89.3 16 160c0 17.7 14.3 32 32 32s32-14.3 32-32zm80 320a40 40 0 1 0 0-80 40 40 0 1 0 0 80z" />
                                                            </svg>
                                                        ) : (
                                                            <CheatLogo cheat={cheat} />
                                                        )
                                                    }
                                                    <span>{cheatsSelectEnuem[cheat]}</span>
                                                </div>
                                            </span>
                                        </Select.Value>
                                        <Select.Icon className="lucide lucide-chevron-down h-4 w-4 opacity-50">
                                            <ChevronDownIcon />
                                        </Select.Icon>
                                    </Select.Trigger>
                                    <Select.Portal>
                                        <Select.Content position="popper" side="bottom" className="relative z-50 max-h-96 min-w-[8rem] overflow-hidden rounded-md border bg-popover/75 backdrop-blur-md text-popover-foreground shadow-md data-[state=open]:animate-in data-[state=closed]:animate-out data-[state=closed]:fade-out-0 data-[state=open]:fade-in-0 data-[state=closed]:zoom-out-95 data-[state=open]:zoom-in-95 data-[side=bottom]:slide-in-from-top-2 data-[side=left]:slide-in-from-right-2 data-[side=right]:slide-in-from-left-2 data-[side=top]:slide-in-from-bottom-2 data-[side=bottom]:translate-y-1 data-[side=left]:-translate-x-1 data-[side=right]:translate-x-1 data-[side=top]:-translate-y-1">
                                            <Select.Viewport className="p-1 h-[var(--radix-select-trigger-height)] w-full min-w-[var(--radix-select-trigger-width)]">
                                                <Select.Group>
                                                    <Select.Item value="gamesense" className="relative flex w-full cursor-default select-none items-center rounded-sm py-1.5 pl-8 pr-2 text-sm outline-none focus:bg-accent focus:text-accent-foreground data-[disabled]:pointer-events-none data-[disabled]:opacity-50">
                                                        {cheat === "gamesense" && <SelectedCheckSVG/>}
                                                        <div className="w-full flex items-center space-x-2">
                                                            <CheatLogo cheat="gamesense" />
                                                            <span>GameSense</span>
                                                        </div>
                                                    </Select.Item>
                                                    <Select.Item value="neverlose" className="relative flex w-full cursor-default select-none items-center rounded-sm py-1.5 pl-8 pr-2 text-sm outline-none focus:bg-accent focus:text-accent-foreground data-[disabled]:pointer-events-none data-[disabled]:opacity-50">
                                                        {cheat === "neverlose" && <SelectedCheckSVG/>}
                                                        <div className="w-full flex items-center space-x-2">
                                                            <CheatLogo cheat="neverlose" />
                                                            <span>NeverLose</span>
                                                        </div>
                                                    </Select.Item>
                                                    <Select.Item value="fatality" className="relative flex w-full cursor-default select-none items-center rounded-sm py-1.5 pl-8 pr-2 text-sm outline-none focus:bg-accent focus:text-accent-foreground data-[disabled]:pointer-events-none data-[disabled]:opacity-50">
                                                        {cheat === "fatality" && <SelectedCheckSVG/>}
                                                        <div className="w-full flex items-center space-x-2">
                                                            <CheatLogo cheat="fatality" />
                                                            <span>Fatality</span>
                                                        </div>
                                                    </Select.Item>
                                                    <Select.Item value="aimware" className="relative flex w-full cursor-default select-none items-center rounded-sm py-1.5 pl-8 pr-2 text-sm outline-none focus:bg-accent focus:text-accent-foreground data-[disabled]:pointer-events-none data-[disabled]:opacity-50">
                                                        {cheat === "aimware" && <SelectedCheckSVG/>}
                                                        <div className="w-full flex items-center space-x-2">
                                                            <CheatLogo cheat="aimware" />
                                                            <span>Aimware</span>
                                                        </div>
                                                    </Select.Item>
                                                    <Select.Item value="obstruction" className="relative flex w-full cursor-default select-none items-center rounded-sm py-1.5 pl-8 pr-2 text-sm outline-none focus:bg-accent focus:text-accent-foreground data-[disabled]:pointer-events-none data-[disabled]:opacity-50">
                                                        {cheat === "obstruction" && <SelectedCheckSVG/>}
                                                        <div className="w-full flex items-center space-x-2">
                                                            <CheatLogo cheat="obstruction" />
                                                            <span>Obstruction</span>
                                                        </div>
                                                    </Select.Item>
                                                    <Select.Item value="other" className="relative flex w-full cursor-default select-none items-center rounded-sm py-1.5 pl-8 pr-2 text-sm outline-none focus:bg-accent focus:text-accent-foreground data-[disabled]:pointer-events-none data-[disabled]:opacity-50">
                                                        {cheat === "other" && <SelectedCheckSVG/>}
                                                        <div className="w-full flex items-center space-x-2">
                                                            <svg stroke="currentColor" fill="currentColor" strokeWidth={0} viewBox="0 0 320 512" className="w-7 h-7 p-1" height="1em" width="1em" xmlns="http://www.w3.org/2000/svg">
                                                                <path d="M80 160c0-35.3 28.7-64 64-64h32c35.3 0 64 28.7 64 64v3.6c0 21.8-11.1 42.1-29.4 53.8l-42.2 27.1c-25.2 16.2-40.4 44.1-40.4 74V320c0 17.7 14.3 32 32 32s32-14.3 32-32v-1.4c0-8.2 4.2-15.8 11-20.2l42.2-27.1c36.6-23.6 58.8-64.1 58.8-107.7V160c0-70.7-57.3-128-128-128H144C73.3 32 16 89.3 16 160c0 17.7 14.3 32 32 32s32-14.3 32-32zm80 320a40 40 0 1 0 0-80 40 40 0 1 0 0 80z" />
                                                            </svg>
                                                            <span>Other</span>
                                                        </div>
                                                    </Select.Item>
                                                    
                                                </Select.Group>
                                            </Select.Viewport>

                                        </Select.Content>
                                    </Select.Portal>
                                </Select.Root>

                                <p id=":r2e:-form-item-description" className="text-sm text-muted-foreground">The cheat that you'll be queuing with.</p>
                            </div>
                            

                            <div className={`space-y-2 ${matche !== "premier" ? 'hidden' : ''}`}>
                                <label className="text-sm font-medium leading-none peer-disabled:cursor-not-allowed peer-disabled:opacity-70" htmlFor=":r2g:-form-item">Min. ELO</label>
                                <input value={minElo} onChange={handleMinEloChange} className="flex h-10 w-full rounded-md border border-input bg-background px-3 py-2 text-sm ring-offset-background file:border-0 file:bg-transparent file:text-sm file:font-medium placeholder:text-muted-foreground focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring focus-visible:ring-offset-2 disabled:cursor-not-allowed disabled:opacity-50" placeholder={20000} min={0} max={35000} step={1000} id=":r2g:-form-item" aria-describedby=":r2g:-form-item-description" aria-invalid="false" type="number" defaultValue={20000} name="minELO" />
                                <p id=":r2g:-form-item-description" className="text-sm text-muted-foreground">Enter your current ELO (skill rating).</p>
                            </div>


                        </div>
                        <div className={`grid grid-cols-1 gap-4 ${!lookingForGame ? 'md:grid-cols-2' : ''}`}>
                            <div className="space-y-2">
                                <label className="text-sm font-medium leading-none peer-disabled:cursor-not-allowed peer-disabled:opacity-70" htmlFor=":r2h:-form-item">Lobby type</label>

                                <Select.Root value={lobby} onValueChange={handleLobby} >
                                    <Select.Trigger className="flex h-10 w-full items-center justify-between rounded-md border border-input bg-background px-3 py-2 text-sm ring-offset-background placeholder:text-muted-foreground focus:outline-none focus:ring-2 focus:ring-ring focus:ring-offset-2 disabled:cursor-not-allowed disabled:opacity-50 [&>span]:line-clamp-1">
                                        <Select.Value>{lobbiesEnuem[lobby]}</Select.Value>
                                        <Select.Icon className="lucide lucide-chevron-down h-4 w-4 opacity-50">
                                            <ChevronDownIcon />
                                        </Select.Icon>
                                    </Select.Trigger>
                                    <Select.Portal>
                                        <Select.Content position="popper" side="bottom" className="relative z-50 max-h-96 min-w-[8rem] overflow-hidden rounded-md border bg-popover/75 backdrop-blur-md text-popover-foreground shadow-md data-[state=open]:animate-in data-[state=closed]:animate-out data-[state=closed]:fade-out-0 data-[state=open]:fade-in-0 data-[state=closed]:zoom-out-95 data-[state=open]:zoom-in-95 data-[side=bottom]:slide-in-from-top-2 data-[side=left]:slide-in-from-right-2 data-[side=right]:slide-in-from-left-2 data-[side=top]:slide-in-from-bottom-2 data-[side=bottom]:translate-y-1 data-[side=left]:-translate-x-1 data-[side=right]:translate-x-1 data-[side=top]:-translate-y-1">
                                            <Select.Viewport className="p-1 h-[var(--radix-select-trigger-height)] w-full min-w-[var(--radix-select-trigger-width)]">
                                                <Select.Group>
                                                    <Select.Item value="any" className="relative flex w-full cursor-default select-none items-center rounded-sm py-1.5 pl-8 pr-2 text-sm outline-none focus:bg-accent focus:text-accent-foreground data-[disabled]:pointer-events-none data-[disabled]:opacity-50">
                                                        {lobby === "any" && <SelectedCheckSVG/>}
                                                        Any
                                                    </Select.Item>
                                                    <Select.Item value="legit" className="relative flex w-full cursor-default select-none items-center rounded-sm py-1.5 pl-8 pr-2 text-sm outline-none focus:bg-accent focus:text-accent-foreground data-[disabled]:pointer-events-none data-[disabled]:opacity-50">
                                                        {lobby === "legit" && <SelectedCheckSVG/>}
                                                        Legit
                                                    </Select.Item>
                                                    <Select.Item value="semi" className="relative flex w-full cursor-default select-none items-center rounded-sm py-1.5 pl-8 pr-2 text-sm outline-none focus:bg-accent focus:text-accent-foreground data-[disabled]:pointer-events-none data-[disabled]:opacity-50">
                                                        {lobby === "semi" && <SelectedCheckSVG/>}
                                                        Semi-Rage
                                                    </Select.Item>
                                                    <Select.Item value="rage" className="relative flex w-full cursor-default select-none items-center rounded-sm py-1.5 pl-8 pr-2 text-sm outline-none focus:bg-accent focus:text-accent-foreground data-[disabled]:pointer-events-none data-[disabled]:opacity-50">
                                                        {lobby === "rage" && <SelectedCheckSVG/>}
                                                        Rage / HvH
                                                    </Select.Item>
                                                    <Select.Item value="grief" className="relative flex w-full cursor-default select-none items-center rounded-sm py-1.5 pl-8 pr-2 text-sm outline-none focus:bg-accent focus:text-accent-foreground data-[disabled]:pointer-events-none data-[disabled]:opacity-50">
                                                        {lobby === "grief" && <SelectedCheckSVG/>}
                                                        Griefing
                                                    </Select.Item>
                                                </Select.Group>
                                            </Select.Viewport>

                                        </Select.Content>
                                    </Select.Portal>
                                </Select.Root>

                                <p id=":r2h:-form-item-description" className="text-sm text-muted-foreground">The type of match that you want to play.</p>
                            </div>
                            <div className={`space-y-2 ${lookingForGame ? "hidden" : ""}`}>
                                <label className="text-sm font-medium leading-none peer-disabled:cursor-not-allowed peer-disabled:opacity-70" htmlFor=":r2j:-form-item">Players needed</label>
                                <input value={players} onChange={handlePlayersChange} className="flex h-10 w-full rounded-md border border-input bg-background px-3 py-2 text-sm ring-offset-background file:border-0 file:bg-transparent file:text-sm file:font-medium placeholder:text-muted-foreground focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring focus-visible:ring-offset-2 disabled:cursor-not-allowed disabled:opacity-50" placeholder={1} min={0} max={4} step={1} id=":r2j:-form-item" aria-describedby=":r2j:-form-item-description" aria-invalid="false" type="number" name="playersNeeded" />
                                <p id=":r2j:-form-item-description" className="text-sm text-muted-foreground">The number of players that you need to fill your lobby.</p>
                            </div>
                        </div>
                        <div className="grid grid-cols-1 gap-4">
                            <div className="space-y-2 flex flex-row items-center justify-between rounded-lg border p-4">
                                <div className="space-y-0.5">
                                    <label className="font-medium peer-disabled:cursor-not-allowed peer-disabled:opacity-70 text-base" htmlFor=":r2k:-form-item">Looking for game</label>
                                    <p id=":r2k:-form-item-description" className="text-sm text-muted-foreground">Are you looking to join an existing lobby?</p>
                                </div>
                                <button onClick={() => setLookingForGame(o => !o)} type="button" role="switch" aria-checked={lookingForGame} data-state={ lookingForGame ? "checked" : "unchecked"} value="on" className="peer inline-flex h-6 w-11 shrink-0 cursor-pointer items-center rounded-full border-2 border-transparent transition-colors focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring focus-visible:ring-offset-2 focus-visible:ring-offset-background disabled:cursor-not-allowed disabled:opacity-50 data-[state=checked]:bg-primary data-[state=unchecked]:bg-input" id=":r2k:-form-item" aria-describedby=":r2k:-form-item-description" aria-invalid="false">
                                    <span data-state={ lookingForGame ? "checked" : "unchecked"} className="pointer-events-none block h-5 w-5 rounded-full bg-background shadow-lg ring-0 transition-transform data-[state=checked]:translate-x-5 data-[state=unchecked]:translate-x-0" />
                                </button>
                            </div>
                        </div>

                        <button className="inline-flex items-center justify-center whitespace-nowrap text-sm font-medium ring-offset-background transition-colors focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring focus-visible:ring-offset-2 disabled:pointer-events-none disabled:opacity-50 bg-primary text-primary-foreground hover:bg-primary/90 h-9 rounded-md px-3" type="submit">Submit</button>
                    </form>
                    
                    <Dialog.Close asChild={true}>
                        <button onClick={() => setModalOpen(false)} type="button" className="absolute right-4 top-4 rounded-sm opacity-70 ring-offset-background transition-opacity hover:opacity-100 focus:outline-none focus:ring-2 focus:ring-ring focus:ring-offset-2 disabled:pointer-events-none data-[state=open]:bg-accent data-[state=open]:text-muted-foreground">
                            <svg xmlns="http://www.w3.org/2000/svg" width={24} height={24} viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth={2} strokeLinecap="round" strokeLinejoin="round" className="lucide lucide-x h-4 w-4">
                                <path d="M18 6 6 18" />
                                <path d="m6 6 12 12" />
                            </svg>
                            <span className="sr-only">Close</span>
                        </button>
                    </Dialog.Close>
                </Dialog.Content>
            </Dialog.Portal>
        </Dialog.Root>
    </>
}

const SelectedCheckSVG = () => {
    return <>
        <span className="absolute left-2 flex h-3.5 w-3.5 items-center justify-center">
            <span aria-hidden="true">
                <svg xmlns="http://www.w3.org/2000/svg" width={24} height={24} viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth={2} strokeLinecap="round" strokeLinejoin="round" className="lucide lucide-check h-4 w-4">
                    <path d="M20 6 9 17l-5-5" />
                </svg>
            </span>
        </span>
    </>
}

const CheatLogo = ({cheat}) => (
    <img
        alt={cheatsSelectEnuem[cheat]}
        loading="lazy"
        width={28}
        height={28}
        decoding="async"
        data-nimg={1}
        className="rounded-lg border w-7 h-7"
        src={`./static/${cheat}.png`}
        style={{color: 'transparent'}}
    />
)

export default AddCodeDialogue