import { useEffect, useState } from "react";
import NetworkFlags from "../NetworkObserver/NetworkFlags"
import Cheats from "./Cheats"
import Ranks from "./Ranks"
import * as Tooltip from '@radix-ui/react-tooltip'

function timeAgo(from, to) {
    const SECOND = 1000;
    const MINUTE = SECOND * 60;
    const HOUR = MINUTE * 60;
    const DAY = HOUR * 24;
    const MONTH = DAY * 30; // Approximation
    const YEAR = DAY * 365; // Approximation

    const elapsed = to - from;

    if (elapsed < MINUTE) {
        return Math.floor(elapsed / SECOND) === 1
            ? "1 second ago"
            : Math.floor(elapsed / SECOND) + " seconds ago";
    } else if (elapsed < HOUR) {
        return Math.floor(elapsed / MINUTE) === 1
            ? "1 minute ago"
            : Math.floor(elapsed / MINUTE) + " minutes ago";
    } else if (elapsed < DAY) {
        return Math.floor(elapsed / HOUR) === 1
            ? "1 hour ago"
            : Math.floor(elapsed / HOUR) + " hours ago";
    } else if (elapsed < MONTH) {
        return Math.floor(elapsed / DAY) === 1
            ? "1 day ago"
            : Math.floor(elapsed / DAY) + " days ago";
    } else if (elapsed < YEAR) {
        return Math.floor(elapsed / MONTH) === 1
            ? "1 month ago"
            : Math.floor(elapsed / MONTH) + " months ago";
    } else {
        return Math.floor(elapsed / YEAR) === 1
            ? "1 year ago"
            : Math.floor(elapsed / YEAR) + " years ago";
    }
}

const matchesEnuem = {
    premier: "Premier",
    competitive: "Competitive",
    wingman: "Wingman"
}

const regionsEnuem = {
    XX: "Unknown",
    NA: "North America",
    EU: "Europe",
    AS: "Asia",
    SA: "South America",
    OC: "Oceania"
}

const lobbiesEnuem = {
    any: "Any",
    legit: "Legit",
    semi: "Semi-Rage",
    rage: "Rage / HvH",
    grief: "Griefing"
}

function regionFlag(region) {
    return ("NA" === region ? "US" : "EU" === region ? "EU" : "AS" === region ? "JP" : "SA" === region ? "BR" : "OC" === region ? "AU" : "XX")
}

function copyBtn(stringText) {

    navigator.clipboard.writeText(stringText)
    
}

const CodeItem = ({code}) => {
    const [codeTime, setCodeTime] = useState(timeAgo(code.time, Date.now()))
    useEffect( () => {
        let timeInt = setInterval( () => {
            setCodeTime(timeAgo(code.time, Date.now()))
        }, 1000 )

        return () => clearInterval(timeInt)
    }, [] )

    return <>
        <tr className="border-b transition-colors hover:bg-muted/50 data-[state=selected]:bg-muted">
            <td className="p-4 align-middle [&:has([role=checkbox])]:pr-0">{matchesEnuem[code?.match?.toLowerCase()]}</td>
            <td className="p-4 align-middle [&:has([role=checkbox])]:pr-0">
                <div className="flex items-center space-x-2">
                    {
                        regionsEnuem[code?.region?.toUpperCase()] ? (
                            code.region === "XX" ? (
                                <svg stroke="currentColor" fill="currentColor" strokeWidth={0} viewBox="0 0 320 512" className="w-9 rounded-md" height="1em" width="1em" xmlns="http://www.w3.org/2000/svg">
                                    <path d="M80 160c0-35.3 28.7-64 64-64h32c35.3 0 64 28.7 64 64v3.6c0 21.8-11.1 42.1-29.4 53.8l-42.2 27.1c-25.2 16.2-40.4 44.1-40.4 74V320c0 17.7 14.3 32 32 32s32-14.3 32-32v-1.4c0-8.2 4.2-15.8 11-20.2l42.2-27.1c36.6-23.6 58.8-64.1 58.8-107.7V160c0-70.7-57.3-128-128-128H144C73.3 32 16 89.3 16 160c0 17.7 14.3 32 32 32s32-14.3 32-32zm80 320a40 40 0 1 0 0-80 40 40 0 1 0 0 80z" />
                                </svg>
                            ) : (
                                <NetworkFlags flag={regionFlag(code.region.toUpperCase()).toLowerCase()} />
                            )
                        ) : (
                            <svg stroke="currentColor" fill="currentColor" strokeWidth={0} viewBox="0 0 320 512" className="w-9 rounded-md" height="1em" width="1em" xmlns="http://www.w3.org/2000/svg">
                                <path d="M80 160c0-35.3 28.7-64 64-64h32c35.3 0 64 28.7 64 64v3.6c0 21.8-11.1 42.1-29.4 53.8l-42.2 27.1c-25.2 16.2-40.4 44.1-40.4 74V320c0 17.7 14.3 32 32 32s32-14.3 32-32v-1.4c0-8.2 4.2-15.8 11-20.2l42.2-27.1c36.6-23.6 58.8-64.1 58.8-107.7V160c0-70.7-57.3-128-128-128H144C73.3 32 16 89.3 16 160c0 17.7 14.3 32 32 32s32-14.3 32-32zm80 320a40 40 0 1 0 0-80 40 40 0 1 0 0 80z" />
                            </svg>  
                        )
                    }
                    <span>
                        {
                            regionsEnuem[code?.region?.toUpperCase()] ? (
                                regionsEnuem[code.region.toUpperCase()]
                            ) : (
                                regionsEnuem['XX']
                            )
                        }
                    </span>
                </div>
            </td>
            <td className="p-4 align-middle [&:has([role=checkbox])]:pr-0">
                <div className="space-x-2 items-center flex">
                    {/*<img src={`https://avatars.steamstatic.com/${code.user.avatar_url}_medium.jpg`} className="w-8 h-8 rounded-full" alt="LogicielX.zip's avatar" />*/}
                    <div className="flex flex-col space-y-1">
                        <div className="flex items-center space-x-2">
                            <span>{code.friendCode}</span>
                        </div>
                        <div className="flex items-center space-x-1">
                            <Cheats cheat={code.cheat} />
                        </div>
                    </div>
                </div>
            </td>
            <td className="p-4 align-middle [&:has([role=checkbox])]:pr-0">LFG - {lobbiesEnuem[code?.lobby?.toLowerCase()]}</td>
            
            {
                code.match === "premier" ? (
                    <>
                        {
                            Number(code.minElo) === 0 ? (
                                <td className="p-4 align-middle [&:has([role=checkbox])]:pr-0">Unranked</td>
                            ) : (
                                <Ranks rank={Number(code.minElo)} />
                            )
                        }
                    </>
                ) : (
                    <td className="p-4 align-middle [&:has([role=checkbox])]:pr-0"></td>
                )
            }
            

            <td className="p-4 align-middle [&:has([role=checkbox])]:pr-0">
                <span>{codeTime}</span>
            </td>
            <td className="p-4 align-middle [&:has([role=checkbox])]:pr-0">
                <div className="flex items-center space-x-3 justify-center">

                    <Tooltip.Provider delayDuration={0}>
                        <Tooltip.Root className="position: fixed; left: 0px; top: 0px; transform: translate(1272px, 351px); min-width: max-content; --radix-popper-transform-origin: 50% 34px; z-index: 50; --radix-popper-available-width: 1560px; --radix-popper-available-height: 384.8125px; --radix-popper-anchor-width: 18.5px; --radix-popper-anchor-height: 16px;">
                            <Tooltip.Trigger asChild={true}>
                                <button onClick={() => copyBtn(code.code)} data-state="closed">
                                    <div className="p-1 action-button text-muted-foreground/50 hover:text-white">
                                        <svg stroke="currentColor" fill="currentColor" strokeWidth={0} viewBox="0 0 384 512" className="w-5 h-5" height="1em" width="1em" xmlns="http://www.w3.org/2000/svg">
                                            <path d="M192 0c-41.8 0-77.4 26.7-90.5 64H64C28.7 64 0 92.7 0 128V448c0 35.3 28.7 64 64 64H320c35.3 0 64-28.7 64-64V128c0-35.3-28.7-64-64-64H282.5C269.4 26.7 233.8 0 192 0zm0 64a32 32 0 1 1 0 64 32 32 0 1 1 0-64zM112 192H272c8.8 0 16 7.2 16 16s-7.2 16-16 16H112c-8.8 0-16-7.2-16-16s7.2-16 16-16z" />
                                        </svg>
                                    </div>
                                </button>
                            </Tooltip.Trigger>
                            <Tooltip.Portal>
                                <Tooltip.Content className="z-50 overflow-hidden rounded-md backdrop-blur-xl border bg-popover px-3 py-1.5 text-sm text-popover-foreground shadow-md animate-in fade-in-0 zoom-in-95 data-[state=closed]:animate-out data-[state=closed]:fade-out-0 data-[state=closed]:zoom-out-95 data-[side=bottom]:slide-in-from-top-2 data-[side=left]:slide-in-from-right-2 data-[side=right]:slide-in-from-left-2 data-[side=top]:slide-in-from-bottom-2" sideOffset={5}>
                                    Copy friend code
                                </Tooltip.Content>
                            </Tooltip.Portal>
                        </Tooltip.Root>
                    </Tooltip.Provider>
                </div>
            </td>

        </tr>
    </>
}

export default CodeItem