import TopBar from "./components/TopBar"
import NavTabs from "./components/NavTabs"
import Matchmaking from "./components/Matchmaking/Matchmaking"
import NetworkObserver from "./components/NetworkObserver/NetworkObserver"
import Footer from "./components/Footer"
import { useState } from "react"



const App = () => {
    const [observerEnabled, setObserverEnabled] = useState(true)
    const [observerSpeed, setObserverSpeed] = useState("normal")

    return <>
        <div role="region" aria-label="Notifications (F8)" tabIndex={-1} style={{pointerEvents: 'none'}}>
            <ol tabIndex={-1} className="fixed top-0 z-[100] flex max-h-screen w-full flex-col-reverse p-4 sm:bottom-0 sm:right-0 sm:top-auto sm:flex-col md:max-w-[420px]" />
        </div>

        <TopBar/>
        <NavTabs/>

        <div className="flex flex-col w-full h-full px-4 mx-auto my-8 space-y-8 max-w-screen-2xl sm:pb-24">
            <div className="flex w-full space-x-4">
                <Matchmaking/>
                <NetworkObserver
                    observerEnabled={observerEnabled}
                    setObserverEnabled={setObserverEnabled}
                    observerSpeed={observerSpeed}
                    setObserverSpeed={setObserverSpeed}
                />
            </div>
        </div>

        <Footer/>
    </>
}

export default App