import { useEffect, useState } from "react"
import NetworkItem from "./NetworkItem"
import { CheckIcon } from '@radix-ui/react-icons';
import * as DropdownMenu from '@radix-ui/react-dropdown-menu';

const serversList = [
    {
        "name": "New York City, NY",
        "endpoint": "us-east-1-nyc-1a",
        "flag": "us"
    },
    {
        "name": "Los Angeles, CA",
        "endpoint": "us-west-2-lax-1a",
        "flag": "us"
    },
    {
        "name": "Chicago, IL",
        "endpoint": "us-east-1-chi-1a",
        "flag": "us"
    },
    {
        "name": "Dallas, TX",
        "endpoint": "us-east-1-dfw-1a",
        "flag": "us"
    },
    {
        "name": "Montreal",
        "endpoint": "ca-central-1",
        "flag": "ca"
    },
    {
        "name": "London",
        "endpoint": "eu-west-2",
        "flag": "gb"
    },
    {
        "name": "Paris",
        "endpoint": "eu-west-3",
        "flag": "fr"
    },
    {
        "name": "Frankfurt",
        "endpoint": "eu-central-1",
        "flag": "de"
    },
    {
        "name": "Stockholm",
        "endpoint": "eu-north-1",
        "flag": "se"
    },
    {
        "name": "Singapore",
        "endpoint": "ap-southeast-1",
        "flag": "sg"
    },
    {
        "name": "Sydney",
        "endpoint": "ap-southeast-2",
        "flag": "au"
    },
    {
        "name": "Tokyo",
        "endpoint": "ap-northeast-1",
        "flag": "jp"
    },
    {
        "name": "So Paulo",
        "endpoint": "sa-east-1",
        "flag": "br"
    },
    {
        "name": "Dubai",
        "endpoint": "me-central-1",
        "flag": "ae"
    },
    {
        "name": "Zurich",
        "endpoint": "eu-central-2",
        "flag": "ch"
    },
    {
        "name": "Poland",
        "endpoint": "eu-central-1-waw-1a",
        "flag": "pl"
    },
    {
        "name": "Delhi",
        "endpoint": "ap-south-1-del-1a",
        "flag": "in"
    },
    {
        "name": "Melbourne",
        "endpoint": "ap-southeast-4",
        "flag": "au"
    },
    {
        "name": "Perth",
        "endpoint": "ap-southeast-2-per-1a",
        "flag": "au"
    },
    {
        "name": "Queretaro",
        "endpoint": "us-east-1-qro-1a",
        "flag": "mx"
    }
]

const NetworkObserver = ({
    observerEnabled,
    setObserverEnabled,
    observerSpeed,
    setObserverSpeed
}) => {
    const [networkLatencies, setNetworkLatencies] = useState({})
    const [networkFilter, setNetworkFilter] = useState('')

    const handleFilterChange = e => {
        setNetworkFilter(e.target.value)
    }

    const toggleObserver = () => {
        setObserverEnabled( old => !old)
    }

    const toggleObserverSpeed = speed => {
        setObserverSpeed(speed)
    }

    return <>
        <div className="hidden xl:flex xl:flex-col xl:space-y-4 xl:w-[25%]">
            <div className="rounded-lg border text-card-foreground shadow-sm relative bg-black/[3%] dark:bg-black/25">

                <DropdownMenu.Root>
                    <DropdownMenu.Trigger asChild={true}>
                        <div className="group absolute top-0 right-0 p-2 cursor-pointer" type="button" id="radix-:rb:" aria-haspopup="menu" aria-expanded="false" data-state="closed">
                            <svg stroke="currentColor" fill="none" strokeWidth={2} viewBox="0 0 24 24" strokeLinecap="round" strokeLinejoin="round" className="w-5 h-5 text-muted-foreground/40 group-hover:text-muted-foreground transition-colors" height="1em" width="1em" xmlns="http://www.w3.org/2000/svg">
                                <path d="M12 20a8 8 0 1 0 0-16 8 8 0 0 0 0 16Z" />
                                <path d="M12 14a2 2 0 1 0 0-4 2 2 0 0 0 0 4Z" />
                                <path d="M12 2v2" />
                                <path d="M12 22v-2" />
                                <path d="m17 20.66-1-1.73" />
                                <path d="M11 10.27 7 3.34" />
                                <path d="m20.66 17-1.73-1" />
                                <path d="m3.34 7 1.73 1" />
                                <path d="M14 12h8" />
                                <path d="M2 12h2" />
                                <path d="m20.66 7-1.73 1" />
                                <path d="m3.34 17 1.73-1" />
                                <path d="m17 3.34-1 1.73" />
                                <path d="m11 13.73-4 6.93" />
                            </svg>
                        </div>
                    </DropdownMenu.Trigger>

                    <DropdownMenu.Portal>
                        <DropdownMenu.Content side="right" className="z-50 min-w-[8rem] overflow-hidden rounded-md border bg-popover/75 backdrop-blur-md p-1 text-popover-foreground shadow-md data-[state=open]:animate-in data-[state=closed]:animate-out data-[state=closed]:fade-out-0 data-[state=open]:fade-in-0 data-[state=closed]:zoom-out-95 data-[state=open]:zoom-in-95 data-[side=bottom]:slide-in-from-top-2 data-[side=left]:slide-in-from-right-2 data-[side=right]:slide-in-from-left-2 data-[side=top]:slide-in-from-bottom-2">
                            <DropdownMenu.CheckboxItem checked={observerEnabled} onCheckedChange={toggleObserver} className="relative flex cursor-default select-none items-center rounded-sm py-1.5 pl-8 pr-2 text-sm outline-none transition-colors focus:bg-accent focus:text-accent-foreground data-[disabled]:pointer-events-none data-[disabled]:opacity-50">
                                <span className="absolute left-2 flex h-3.5 w-3.5 items-center justify-center">
                                    {
                                        observerEnabled ? (
                                            <svg xmlns="http://www.w3.org/2000/svg" width={24} height={24} viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth={2} strokeLinecap="round" strokeLinejoin="round" className="lucide lucide-check h-4 w-4">
                                                <path d="M20 6 9 17l-5-5" />
                                            </svg>
                                        ) : ( false )
                                    }
                                </span>
                                Enabled
                            </DropdownMenu.CheckboxItem>
                            <DropdownMenu.Sub>
                                <DropdownMenu.SubTrigger asChild={true}>
                                    <DropdownMenu.Item className="flex cursor-default select-none items-center rounded-sm px-2 py-1.5 text-sm outline-none focus:bg-accent data-[state=open]:bg-accent">
                                        Update rate
                                        <svg xmlns="http://www.w3.org/2000/svg" width={24} height={24} viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth={2} strokeLinecap="round" strokeLinejoin="round" className="lucide lucide-chevron-right ml-auto h-4 w-4">
                                            <path d="m9 18 6-6-6-6" />
                                        </svg>
                                    </DropdownMenu.Item>
                                </DropdownMenu.SubTrigger>
                                <DropdownMenu.Portal>
                                    <DropdownMenu.SubContent className="z-50 min-w-[8rem] overflow-hidden rounded-md border bg-popover/75 backdrop-blur-md p-1 text-popover-foreground shadow-lg data-[state=open]:animate-in data-[state=closed]:animate-out data-[state=closed]:fade-out-0 data-[state=open]:fade-in-0 data-[state=closed]:zoom-out-95 data-[state=open]:zoom-in-95 data-[side=bottom]:slide-in-from-top-2 data-[side=left]:slide-in-from-right-2 data-[side=right]:slide-in-from-left-2 data-[side=top]:slide-in-from-bottom-2" >
                                        <DropdownMenu.CheckboxItem checked={observerSpeed === "fast"} onCheckedChange={() => toggleObserverSpeed("fast")} className="relative flex cursor-default select-none items-center rounded-sm py-1.5 pl-8 pr-2 text-sm outline-none transition-colors focus:bg-accent focus:text-accent-foreground data-[disabled]:pointer-events-none data-[disabled]:opacity-50">
                                            <span className="absolute left-2 flex h-3.5 w-3.5 items-center justify-center">
                                                {
                                                    observerSpeed === "fast" ? (
                                                        <svg xmlns="http://www.w3.org/2000/svg" width={24} height={24} viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth={2} strokeLinecap="round" strokeLinejoin="round" className="lucide lucide-check h-4 w-4">
                                                            <path d="M20 6 9 17l-5-5" />
                                                        </svg>
                                                    ) : ( false )
                                                }
                                            </span>
                                            Fast
                                        </DropdownMenu.CheckboxItem>
                                        <DropdownMenu.CheckboxItem checked={observerSpeed === "normal"} onCheckedChange={() => toggleObserverSpeed("normal")} className="relative flex cursor-default select-none items-center rounded-sm py-1.5 pl-8 pr-2 text-sm outline-none transition-colors focus:bg-accent focus:text-accent-foreground data-[disabled]:pointer-events-none data-[disabled]:opacity-50">
                                            <span className="absolute left-2 flex h-3.5 w-3.5 items-center justify-center">
                                                {
                                                    observerSpeed === "normal" ? (
                                                        <svg xmlns="http://www.w3.org/2000/svg" width={24} height={24} viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth={2} strokeLinecap="round" strokeLinejoin="round" className="lucide lucide-check h-4 w-4">
                                                            <path d="M20 6 9 17l-5-5" />
                                                        </svg>
                                                    ) : ( false )
                                                }
                                            </span>
                                            Normal
                                        </DropdownMenu.CheckboxItem>
                                        <DropdownMenu.CheckboxItem checked={observerSpeed === "slow"} onCheckedChange={() => toggleObserverSpeed("slow")} className="relative flex cursor-default select-none items-center rounded-sm py-1.5 pl-8 pr-2 text-sm outline-none transition-colors focus:bg-accent focus:text-accent-foreground data-[disabled]:pointer-events-none data-[disabled]:opacity-50">
                                            <span className="absolute left-2 flex h-3.5 w-3.5 items-center justify-center">
                                                {
                                                    observerSpeed === "slow" ? (
                                                        <svg xmlns="http://www.w3.org/2000/svg" width={24} height={24} viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth={2} strokeLinecap="round" strokeLinejoin="round" className="lucide lucide-check h-4 w-4">
                                                            <path d="M20 6 9 17l-5-5" />
                                                        </svg>
                                                    ) : ( false )
                                                }
                                            </span>
                                            Slow
                                        </DropdownMenu.CheckboxItem>
                                        
                                    </DropdownMenu.SubContent>
                                </DropdownMenu.Portal>

                            </DropdownMenu.Sub>
                        </DropdownMenu.Content>
                    </DropdownMenu.Portal>
                </DropdownMenu.Root>

                <div className="flex flex-col space-y-1.5 p-6">
                    <h3 className="text-2xl font-semibold leading-none tracking-tight flex items-center space-x-2">
                        <svg stroke="currentColor" fill="none" strokeWidth={2} viewBox="0 0 24 24" strokeLinecap="round" strokeLinejoin="round" className="text-xl" height="1em" width="1em" xmlns="http://www.w3.org/2000/svg">
                            <rect x={16} y={16} width={6} height={6} rx={1} />
                            <rect x={2} y={16} width={6} height={6} rx={1} />
                            <rect x={9} y={2} width={6} height={6} rx={1} />
                            <path d="M5 16v-3a1 1 0 0 1 1-1h12a1 1 0 0 1 1 1v3" />
                            <path d="M12 12V8" />
                        </svg>
                        <span>Network Observer</span>
                    </h3>
                    <p className="text-sm text-muted-foreground">Test your ping against common server locations</p>
                    <p className="text-muted-foreground items-center space-x-2 text-xs dark:text-white/50">
                        <svg stroke="currentColor" fill="currentColor" strokeWidth={0} viewBox="0 0 512 512" className="inline w-4 h-4" height="1em" width="1em" xmlns="http://www.w3.org/2000/svg">
                            <path d="M256 32c14.2 0 27.3 7.5 34.5 19.8l216 368c7.3 12.4 7.3 27.7 .2 40.1S486.3 480 472 480H40c-14.3 0-27.6-7.7-34.7-20.1s-7-27.8 .2-40.1l216-368C228.7 39.5 241.8 32 256 32zm0 128c-13.3 0-24 10.7-24 24V296c0 13.3 10.7 24 24 24s24-10.7 24-24V184c0-13.3-10.7-24-24-24zm32 224a32 32 0 1 0 -64 0 32 32 0 1 0 64 0z" />
                        </svg>
                        <span>These values are only for approximation.</span>
                    </p>
                </div>
                
                {
                    observerEnabled ? (
                        <>
                            <div className="p-6 pt-0 overflow-auto flex flex-col space-y-4 max-h-[48rem]">
                                <div className="flex flex-col pt-2">
                                    <input onChange={handleFilterChange} className="flex h-10 w-full rounded-md border border-input bg-background px-3 py-2 text-sm ring-offset-background file:border-0 file:bg-transparent file:text-sm file:font-medium placeholder:text-muted-foreground focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring focus-visible:ring-offset-2 disabled:cursor-not-allowed disabled:opacity-50" placeholder="Filter 20 locations..." />
                                </div>
                                <div className="flex flex-col space-y-2">
                                    {
                                        serversList.sort((e,t)=>{
                                            var r, a;
                                            let s = null !== (r = networkLatencies[e.name]) && void 0 !== r ? r : -1
                                              , n = null !== (a = networkLatencies[t.name]) && void 0 !== a ? a : -1;
                                            return -1 === s ? 1 : s - n
                                        })
                                        .map( (server, i) =>
                                            <NetworkItem
                                                hidden={!server.name.toLowerCase().includes(networkFilter.toLowerCase())}
                                                best={i === 0}
                                                key={server.name}
                                                server={server}
                                                setNetworkLatencies={setNetworkLatencies}
                                                observerSpeed={observerSpeed}
                                            />
                                        )
                                    }
                                </div>
                            </div>
                        </>
                    ) : (
                        <>
                            <div className="p-6 pt-0 overflow-auto flex flex-col space-y-4 max-h-[48rem] justify-center">
                                <div className="flex-grow flex flex-col items-center justify-center space-y-2 px-4 py-16 text-center">
                                    <svg stroke="currentColor" fill="none" strokeWidth={2} viewBox="0 0 24 24" strokeLinecap="round" strokeLinejoin="round" className="w-16 h-16 text-primary" height="1em" width="1em" xmlns="http://www.w3.org/2000/svg">
                                        <circle cx={12} cy={12} r={10} />
                                        <path d="m15 9-6 6" />
                                        <path d="m9 9 6 6" />
                                    </svg>
                                    <span className="text-sm">You have disabled the network observer.</span>
                                    <button onClick={toggleObserver} className="inline-flex items-center justify-center whitespace-nowrap text-sm font-medium ring-offset-background transition-colors focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring focus-visible:ring-offset-2 disabled:pointer-events-none disabled:opacity-50 bg-primary text-primary-foreground hover:bg-primary/90 h-9 rounded-md px-3">Enable Network Observer</button>
                                </div>
                            </div>
                        </>
                    )
                }
                

            </div>
        </div>
    </>
}

export default NetworkObserver