const ranksUrls = {
    0: "static/ranks/tier-0.svg",
    1: "static/ranks/tier-1.svg",
    2: "static/ranks/tier-2.svg",
    3: "static/ranks/tier-3.svg",
    4: "static/ranks/tier-4.svg",
    5: "static/ranks/tier-5.svg",
    6: "static/ranks/tier-6.svg"
}

const ranksColors = {
    0: "#C4D0DE",
    1: "#8CC6FF",
    2: "#6A7DFF",
    3: "#C166FF",
    4: "#F03CFF",
    5: "#EB4B4B",
    6: "#FFD700"
}

const getNumAfterThusand = t=>{
    let e = String((t /= 1e3).toFixed(3)).padStart(6, "0");
    return [Number(e.slice(0, 2)), e.slice(-3)]
}
const getRankIndex = t=>{
    let e = Math.floor(+getNumAfterThusand(t)[0] / 5);
    return e < 0 ? 0 : e > 6 ? 6 : e
}

const Ranks = ({rank}) => {

    return <>
        {
            rank === "unranked" ? (
                <td className="p-4 align-middle [&:has([role=checkbox])]:pr-0">
                    unranked
                </td>
            ) : (
                <td className="p-4 align-middle [&:has([role=checkbox])]:pr-0" style={{backgroundImage: `url(${ranksUrls[getRankIndex(rank)]})`, backgroundRepeat: 'no-repeat', backgroundSize: '5.5em', backgroundPosition: '0px center'}}>
                    <div className="italic font-bold" style={{fontFamily: 'stratum-2-web, sans-serif', color: ranksColors[getRankIndex(rank)], textShadow: 'rgb(0, 0, 0) 0.05em 0.05em 0.15em'}}>
                        <span className="text-xl leading-none">{getNumAfterThusand(rank)[0]}</span>
                        <span className="text-[1.05rem]">,{getNumAfterThusand(rank)[1]}</span>
                    </div>
                </td>
            )
        }
    </>
}

export default Ranks