import { useEffect, useRef, useState } from "react";
import NetworkFlags from "./NetworkFlags"
import { AreaChart, Area, ResponsiveContainer } from 'recharts';
import * as Tooltip from '@radix-ui/react-tooltip'
import * as Dialog from '@radix-ui/react-dialog'

const speedEnuem = {
    fast: 1000,
    normal: 2500,
    slow: 5000
}


const NetworkItem = ({
    server,
    best,
    hidden,
    setNetworkLatencies,
    observerSpeed
}) => {
    const isUnmounted = useRef(false)
    const [latency, setLatency] = useState(-1)
    const [smoothLatency, setSmoothLatency] = useState(-1)
    const [networkPoints, setNetworkPoints] = useState([])

    useEffect(() => {
        let timeout
        let img = new Image()

        const cleanup = () => {
            if (img) {
                img.onload = null
                img.onerror = null
                img = null
            }
        }

        const checkLatency = () => {
            if (isUnmounted.current) return

            isUnmounted.current = true
            timeout = setTimeout(updateLatency, speedEnuem[observerSpeed])

            const entries = performance.getEntriesByName(img.src)
            const lastEntry = entries[entries.length - 1]

            performance.clearResourceTimings()
            cleanup()

            if (!lastEntry) return;

            const currentLatency = Math.floor(lastEntry.duration)

            if (currentLatency === 0) {
                setLatency(-2)
            } else if (currentLatency !== latency) {
                setSmoothLatency(smoothLatency + (Math.abs(currentLatency - latency) - smoothLatency) / 16)
                setLatency(currentLatency)
            }
        }

        img.onload = checkLatency;
        img.onerror = checkLatency;

        const updateLatency = () => {
            img = new Image()
            img.src = `https://${server.endpoint}.aws-latency-test.com/?_c${Math.floor(1000 * Math.random())}=${new Date().getTime()}`;
            img.onload = checkLatency;
            img.onerror = checkLatency;
            isUnmounted.current = false;
        }

        timeout = setTimeout(() => {
            updateLatency()
        }, speedEnuem[observerSpeed])

        return () => {
            clearTimeout(timeout)
            isUnmounted.current = true
        }
    }, [observerSpeed])

    const [colorStatus, setColorStatus] = useState(["text-white/50", "Waiting for connection..."])
    const [latencyLoaded, setLatencyLoaded] = useState(["text-white/50", "Waiting for connection..."])

    useEffect( () => {

        const [textColor,status,chartColor] = -1 !== latency ? latency <= 20 ? ["text-green-400", "excellent", "#6969ff"] : latency <= 50 ? ["text-green-500", "good", "#49ff49"] : latency <= 80 ? ["text-orange-500/75", "acceptable", "#ff9f49"] : latency <= 120 ? ["text-orange-500", "not great", "#ff9f49"] : ["text-red-500/75", "terrible", "#ff4949"] : ["text-white/50", "Waiting for connection..."]
        if( -1 === latency ) {
            setLatencyLoaded(false)
        } else {
            setNetworkLatencies( oldLatencies => {
                return {
                    ...oldLatencies,
                    [server.name]: latency
                }
            } )
            setLatencyLoaded(true)

            setNetworkPoints(oldPoints=> {
                return [
                    ...oldPoints.slice(-25),
                    {
                        time: Date.now(),
                        value: latency
                    }
                ]
            })
        }

        setColorStatus([textColor, status, chartColor])
    }, [latency, server.name, setLatencyLoaded, setNetworkLatencies] )

    return <>
        <div className={`relative flex items-center justify-between px-4 py-2 rounded-lg bg-black/10 hover:bg-black/25 dark:hover:bg-white/5 dots transition border ${ best ? 'border-primary' : 'border-black/10 dark:border-white/10' } ${hidden ? 'hidden' : ''}`}>
            <div className="absolute top-0 left-0 z-0 w-full h-full transition-all duration-1000" style={{pointerEvents: 'none', opacity: "".concat(networkPoints.length / 25 * 25, "%") }}>
                {
                    latencyLoaded ? (
                        <div className="recharts-responsive-container rounded-lg" style={{width: '100%', height: '100%', minWidth: '0px'}}>
                            
                            {/*<div className="recharts-wrapper rounded-lg" role="region" style={{position: 'relative', cursor: 'default', width: '100%', height: '100%', maxHeight: '56px', maxWidth: '250px'}}>
                                <svg className="recharts-surface" width={250} height={56} viewBox="0 0 250 56" style={{width: '100%', height: '100%'}}>
                                    <title />
                                    <desc />
                                    <defs>
                                        <clipPath id="recharts19-clip">
                                            <rect x={0} y={15} height={41} width={250} />
                                        </clipPath>
                                    </defs>
                                    <g className="recharts-layer recharts-area">
                                        <g className="recharts-layer">
                                            <path name="Value" fill={best ? "#6969ff" :colorStatus[2]} fillOpacity="0.6" width={250} height={41} stroke="none" className="recharts-curve recharts-area-area" d="M0,24.5C2.53,25.75,5.06,27,7.59,27C10.111,27,12.632,26.666,15.153,26.5C17.682,26.333,20.212,26.333,22.741,26C25.266,25.667,27.791,24,30.316,24C32.844,24,35.371,27,37.899,27C40.422,27,42.946,24.5,45.469,24.5C47.997,24.5,50.524,27,53.052,27C58.097,27,63.142,26.5,68.187,26.5C70.719,26.5,73.251,27.5,75.783,27.5C78.307,27.5,80.831,27.139,83.355,27C88.404,26.722,93.452,26.5,98.501,26.5C101.027,26.5,103.552,27.5,106.078,27.5C108.621,27.5,111.164,23,113.707,23C116.227,23,118.748,26.933,121.269,27C133.89,27.333,146.51,27.5,159.13,27.5C164.183,27.5,169.236,26.386,174.288,25.5C176.823,25.055,179.359,24,181.894,24C184.407,24,186.92,26.668,189.434,27C191.958,27.333,194.482,27.5,197.006,27.5C199.531,27.5,202.055,22.5,204.579,22.5C206.735,22.5,208.892,27.5,211.048,27.5C215.384,27.5,219.72,15,224.056,15C226.225,15,228.394,26.328,230.563,27C232.716,27.667,234.869,28,237.022,28C241.348,28,245.674,27.75,250,27.5L250,56C245.674,56,241.348,56,237.022,56C234.869,56,232.716,56,230.563,56C228.394,56,226.225,56,224.056,56C219.72,56,215.384,56,211.048,56C208.892,56,206.735,56,204.579,56C202.055,56,199.531,56,197.006,56C194.482,56,191.958,56,189.434,56C186.92,56,184.407,56,181.894,56C179.359,56,176.823,56,174.288,56C169.236,56,164.183,56,159.13,56C146.51,56,133.89,56,121.269,56C118.748,56,116.227,56,113.707,56C111.164,56,108.621,56,106.078,56C103.552,56,101.027,56,98.501,56C93.452,56,88.404,56,83.355,56C80.831,56,78.307,56,75.783,56C73.251,56,70.719,56,68.187,56C63.142,56,58.097,56,53.052,56C50.524,56,47.997,56,45.469,56C42.946,56,40.422,56,37.899,56C35.371,56,32.844,56,30.316,56C27.791,56,25.266,56,22.741,56C20.212,56,17.682,56,15.153,56C12.632,56,10.111,56,7.59,56C5.06,56,2.53,56,0,56Z" />
                                            <path name="Value" stroke="#ffffff9f" fill="none" fillOpacity="0.6" width={250} height={41} className="recharts-curve recharts-area-curve" d="M0,24.5C2.53,25.75,5.06,27,7.59,27C10.111,27,12.632,26.666,15.153,26.5C17.682,26.333,20.212,26.333,22.741,26C25.266,25.667,27.791,24,30.316,24C32.844,24,35.371,27,37.899,27C40.422,27,42.946,24.5,45.469,24.5C47.997,24.5,50.524,27,53.052,27C58.097,27,63.142,26.5,68.187,26.5C70.719,26.5,73.251,27.5,75.783,27.5C78.307,27.5,80.831,27.139,83.355,27C88.404,26.722,93.452,26.5,98.501,26.5C101.027,26.5,103.552,27.5,106.078,27.5C108.621,27.5,111.164,23,113.707,23C116.227,23,118.748,26.933,121.269,27C133.89,27.333,146.51,27.5,159.13,27.5C164.183,27.5,169.236,26.386,174.288,25.5C176.823,25.055,179.359,24,181.894,24C184.407,24,186.92,26.668,189.434,27C191.958,27.333,194.482,27.5,197.006,27.5C199.531,27.5,202.055,22.5,204.579,22.5C206.735,22.5,208.892,27.5,211.048,27.5C215.384,27.5,219.72,15,224.056,15C226.225,15,228.394,26.328,230.563,27C232.716,27.667,234.869,28,237.022,28C241.348,28,245.674,27.75,250,27.5" />
                                        </g>
                                    </g>
                                </svg>
                            </div>*/}

                            {
                                !hidden ? (
                                    <ResponsiveContainer className='rounded-lg' width={'100%'} height={'100%'}>
                                        <AreaChart className='rounded-lg' data={networkPoints} margin={{ top: 15, right: 0, left: 0, bottom: 0}}>
                                            <Area
                                                type="monotone"
                                                dataKey="value"
                                                name="value"
                                                
                                                stroke="#ffffff9f"
                                                fill={best ? "#6969ff" :colorStatus[2]}
                                                isAnimationActive={false}
                                            />
                                        </AreaChart>
                                    </ResponsiveContainer>
                                ) : (false)
                            }

                        </div>
                    ) : ( false )
                }

            </div>
            <div className="flex items-center space-x-3 z-10">
                <NetworkFlags flag={server.flag} />
                <div className="flex flex-col">
                    <div className="flex items-center space-x-2">
                        <span>{server.name}</span>
                        {
                            best ? (
                                <button data-state="closed">
                                    <svg stroke="currentColor" fill="currentColor" strokeWidth={0} viewBox="0 0 576 512" className="w-3 h-3 text-orange-300" height="1em" width="1em" xmlns="http://www.w3.org/2000/svg">
                                        <path d="M316.9 18C311.6 7 300.4 0 288.1 0s-23.4 7-28.8 18L195 150.3 51.4 171.5c-12 1.8-22 10.2-25.7 21.7s-.7 24.2 7.9 32.7L137.8 329 113.2 474.7c-2 12 3 24.2 12.9 31.3s23 8 33.8 2.3l128.3-68.5 128.3 68.5c10.8 5.7 23.9 4.9 33.8-2.3s14.9-19.3 12.9-31.3L438.5 329 542.7 225.9c8.6-8.5 11.7-21.2 7.9-32.7s-13.7-19.9-25.7-21.7L381.2 150.3 316.9 18z" />
                                    </svg>
                                </button>
                            ) : ( false )
                        }

                    </div>
                    <div className="flex space-x-1 text-xs dark:text-white/50">
                        {
                            latencyLoaded ? (
                                <>
                                    <span className={`${colorStatus[0]} font-mono`}>{latency}ms</span>
                                    <span>+-</span>
                                    <Tooltip.Provider delayDuration={0}>
                                        <Tooltip.Root className="position: fixed; left: 0px; top: 0px; transform: translate(1272px, 351px); min-width: max-content; --radix-popper-transform-origin: 50% 34px; z-index: 50; --radix-popper-available-width: 1560px; --radix-popper-available-height: 384.8125px; --radix-popper-anchor-width: 18.5px; --radix-popper-anchor-height: 16px;">
                                            <Tooltip.Trigger asChild={true}>
                                                <span className="text-[0.7rem] font-mono hover:cursor-pointer" data-state="closed">{smoothLatency.toFixed(0)}ms</span>
                                            </Tooltip.Trigger>
                                            <Tooltip.Portal>
                                                <Tooltip.Content className="z-50 overflow-hidden rounded-md backdrop-blur-xl border bg-popover px-3 py-1.5 text-sm text-popover-foreground shadow-md animate-in fade-in-0 zoom-in-95 data-[state=closed]:animate-out data-[state=closed]:fade-out-0 data-[state=closed]:zoom-out-95 data-[side=bottom]:slide-in-from-top-2 data-[side=left]:slide-in-from-right-2 data-[side=right]:slide-in-from-left-2 data-[side=top]:slide-in-from-bottom-2" sideOffset={5}>
                                                    Jitter
                                                </Tooltip.Content>
                                            </Tooltip.Portal>
                                        </Tooltip.Root>
                                    </Tooltip.Provider>
                                    <span></span>
                                </>
                            ) : ( false )

                        }
                        <span>{colorStatus[1]}</span>
                    </div>
                </div>
            </div>
            
                {
                    latencyLoaded ? (
                        <div className="border text-xs font-semibold focus:outline-none focus:ring-2 focus:ring-ring focus:ring-offset-2 border-transparent bg-affirmative text-affirmative-foreground flex items-center p-1 space-x-1 transition rounded-full">
                            <svg stroke="currentColor" fill="none" strokeWidth={2} viewBox="0 0 24 24" strokeLinecap="round" strokeLinejoin="round" className="w-3 h-3" height="1em" width="1em" xmlns="http://www.w3.org/2000/svg">
                                <polyline points="20 6 9 17 4 12" />
                            </svg>
                        </div>
                    ) : (
                        <div className="border text-xs font-semibold focus:outline-none focus:ring-2 focus:ring-ring focus:ring-offset-2 border-transparent bg-primary text-primary-foreground hover:bg-primary/80 flex items-center p-1 space-x-1 transition rounded-full">
                            <svg stroke="currentColor" fill="currentColor" strokeWidth={0} viewBox="0 0 512 512" className="w-3 h-3 animate-spin" height="1em" width="1em" xmlns="http://www.w3.org/2000/svg">
                                <path d="M304 48a48 48 0 1 0 -96 0 48 48 0 1 0 96 0zm0 416a48 48 0 1 0 -96 0 48 48 0 1 0 96 0zM48 304a48 48 0 1 0 0-96 48 48 0 1 0 0 96zm464-48a48 48 0 1 0 -96 0 48 48 0 1 0 96 0zM142.9 437A48 48 0 1 0 75 369.1 48 48 0 1 0 142.9 437zm0-294.2A48 48 0 1 0 75 75a48 48 0 1 0 67.9 67.9zM369.1 437A48 48 0 1 0 437 369.1 48 48 0 1 0 369.1 437z" />
                            </svg>
                        </div>
                    )
                }

        </div>
    </>
}

export default NetworkItem