const cheatsEnum = {
    gamesense: "GameSense",
    neverlose: "Neverlose",
    fatality: "Fatality",
    aimware: "Aimware",
    obstruction: "Obstruction",
    other: "Other"
}
const cheatsLogos = {
    gamesense: "/logos/cheats/gamesense.png",
    neverlose: "/logos/cheats/neverlose.png",
    fatality: "/logos/cheats/fatality.png",
    aimware: "/logos/cheats/aimware.png",
    obstruction: "/logos/cheats/obstruction.png"
}

const OtherCheat = () => (
    <svg stroke="currentColor" fill="currentColor" strokeWidth={0} viewBox="0 0 320 512" className="w-5 h-5 p-1 border rounded-lg" height="1em" width="1em" xmlns="http://www.w3.org/2000/svg">
        <path d="M80 160c0-35.3 28.7-64 64-64h32c35.3 0 64 28.7 64 64v3.6c0 21.8-11.1 42.1-29.4 53.8l-42.2 27.1c-25.2 16.2-40.4 44.1-40.4 74V320c0 17.7 14.3 32 32 32s32-14.3 32-32v-1.4c0-8.2 4.2-15.8 11-20.2l42.2-27.1c36.6-23.6 58.8-64.1 58.8-107.7V160c0-70.7-57.3-128-128-128H144C73.3 32 16 89.3 16 160c0 17.7 14.3 32 32 32s32-14.3 32-32zm80 320a40 40 0 1 0 0-80 40 40 0 1 0 0 80z" />
    </svg>
)

const CheatLogo = ({cheat}) => (
    <img
        alt="Neverlose"
        loading="lazy"
        width={22}
        height={22}
        decoding="async"
        data-nimg={1}
        className="rounded-lg border"
        src={`./static/${cheat}.png`}
        style={{color: 'transparent'}}
    /> 
)

const Cheats = ({cheat}) => {

    return <>
        {
            cheatsEnum[cheat?.toLowerCase()] ? (
                cheat === "other" ? <OtherCheat/> : <CheatLogo cheat={cheat}/>
            ) : (
                <OtherCheat/>
            )
        }

        <span className="text-xs text-muted-foreground">{cheatsEnum[cheat?.toLowerCase()] ? cheatsEnum[cheat.toLowerCase()] : cheatsEnum['other']}</span>
    </>
}

export default Cheats