import { useEffect, useState } from "react"
import CodeItem from "./CodeItem"
import * as Select from '@radix-ui/react-select'
import { CheckIcon, ChevronDownIcon, ChevronUpIcon } from '@radix-ui/react-icons'
import * as Dialog from '@radix-ui/react-dialog';
import * as Tooltip from '@radix-ui/react-tooltip'
import config from "../../../config"
import AddCodeDialogue from "../NetworkObserver/AddCodeDialogue";

const matchesSelectEnuem = {
    all: "All matches",
    premier: "Premere",
    competitive: "Competitive",
    wingman: "Wingman"
}

const cheatsSelectEnuem = {
    all: "All cheats",
    gamesense: "GameSense",
    neverlose: "Neverlose",
    fatality: "Fatality",
    aimware: "Aimware",
    obstruction: "Obstruction",
    other: "Other"
}

const lobbiesEnuem = {
    all: "All lobbies",
    legit: "Legit",
    semi: "Semi-Rage",
    rage: "Rage / HvH",
    grief: "Griefing"
}

const testCodesList = [
    {
      type: 'premier',
      region: 'EU',
      cheat: 'other',
      lobby: 'any',
      code: 'TEST CODE',
      minELO: 30000,
      time: 1715329800000,
      user: {
        steamid: '76561198197090537',
        accountid: 236824809,
        persona_name: 'LogicielX.zip',
        avatar_url: 'b2edbc430cffa963966ebafc9e7844414a9c51c1',
        profile_url: '',
        persona_state: 1,
        city: '',
        state: '',
        country: 'DZ',
        real_name: 'torch3yes',
        is_friend: false,
        friends_in_common: 0
      }
    }
  ]

let minEloTimeout
const Matchmaking = () => {
    const [codesList, setCodesList] = useState([])
    const [matchesFilter, setMatchesFilter] = useState("all")
    const [cheatFilter, setCheatFilter] = useState("all")
    const [lobbyFilter, setLobbyFilter] = useState("all")
    const [minEloFilterValue, setMinEloFilterValue] = useState("")
    const [minEloFilter, setMinEloFilter] = useState("")
    const [notificationsSound, setNotificationsSound] = useState(true)

    useEffect( () => {

        fetch( `${config.BASE_URL}/api/matchmaking` )
            .then( res => res.json() )
            .then( res => {
                setCodesList(res)
            } )
            .catch( err => {
                console.error('error fetching matchmaking data:', err.message)
            } )

    }, [] )

    useEffect( () => {
        clearTimeout(minEloTimeout)
        minEloTimeout = setTimeout( () => {
            setMinEloFilter(minEloFilterValue)
            const urlParams = new URLSearchParams(window.location.search)

            let newUrl
            if( minEloFilterValue === "" ) {
                urlParams.delete('elo')
                newUrl = window.location.pathname
            } else {
                urlParams.set('elo', minEloFilterValue)
                newUrl = window.location.pathname + '?' + urlParams.toString()
            }
            
            window.history.pushState(null, null, newUrl)
        }, 800 )

        return () => {
            clearTimeout(minEloTimeout)
        }
    }, [minEloFilterValue] )

    const handleMatchesFilterChange = value => {
        setMatchesFilter(value)
    }

    const handleCheatFilterChange = value => {
        setCheatFilter(value)
    }

    const handleLobbyFilterChange = value => {
        setLobbyFilter(value)
    }

    const handleMinEloChange = e => {
        setMinEloFilterValue(e.target.value.trim())
    }


    return <>
        <div className="w-full xl:w-[75%]">
            <div className="flex flex-col space-y-8">
                <div className="flex justify-between items-center">
                    <div className="flex items-center px-4 space-x-4">
                        <div className="flex h-fit justify-center items-center bg-primary p-3 rounded-lg">
                            <svg stroke="currentColor" fill="none" strokeWidth={2} viewBox="0 0 24 24" strokeLinecap="round" strokeLinejoin="round" className="w-5 h-5 text-white" height="1em" width="1em" xmlns="http://www.w3.org/2000/svg">
                                <path d="M21.54 15H17a2 2 0 0 0-2 2v4.54" />
                                <path d="M7 3.34V5a3 3 0 0 0 3 3v0a2 2 0 0 1 2 2v0c0 1.1.9 2 2 2v0a2 2 0 0 0 2-2v0c0-1.1.9-2 2-2h3.17" />
                                <path d="M11 21.95V18a2 2 0 0 0-2-2v0a2 2 0 0 1-2-2v-1a2 2 0 0 0-2-2H2.05" />
                                <circle cx={12} cy={12} r={10} />
                            </svg>
                        </div>
                        <div className="flex flex-col">
                            <h1 className="text-2xl">Matchmaking</h1>
                            <p className="text-sm text-muted-foreground">Share your friend code here and easily matchmake with other players.</p>
                        </div>
                    </div>

                    <Tooltip.Provider delayDuration={0}>
                        <Tooltip.Root className="position: fixed; left: 0px; top: 0px; transform: translate(1272px, 351px); min-width: max-content; --radix-popper-transform-origin: 50% 34px; z-index: 50; --radix-popper-available-width: 1560px; --radix-popper-available-height: 384.8125px; --radix-popper-anchor-width: 18.5px; --radix-popper-anchor-height: 16px;">
                            <Tooltip.Trigger asChild={true}>
                                <div className="flex items-center">
                                    <button data-state="closed" />
                                    <button onClick={() => setNotificationsSound(o => !o)} className="inline-flex items-center justify-center whitespace-nowrap rounded-md text-sm font-medium ring-offset-background transition-colors focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring focus-visible:ring-offset-2 disabled:pointer-events-none disabled:opacity-50 border border-input bg-black/10 dark:bg-black/25 hover:bg-primary hover:text-primary-foreground h-10 px-4 py-2">
                                        {
                                            notificationsSound ? (
                                                <svg stroke="currentColor" fill="none" strokeWidth={2} viewBox="0 0 24 24" strokeLinecap="round" strokeLinejoin="round" className="w-5 h-5" height="1em" width="1em" xmlns="http://www.w3.org/2000/svg">
                                                    <path d="M6 8a6 6 0 0 1 12 0c0 7 3 9 3 9H3s3-2 3-9" />
                                                    <path d="M10.3 21a1.94 1.94 0 0 0 3.4 0" />
                                                </svg>
                                            ) : (
                                                <svg stroke="currentColor" fill="none" strokeWidth={2} viewBox="0 0 24 24" strokeLinecap="round" strokeLinejoin="round" className="w-5 h-5 text-muted-foreground" height="1em" width="1em" xmlns="http://www.w3.org/2000/svg">
                                                    <path d="M8.7 3A6 6 0 0 1 18 8a21.3 21.3 0 0 0 .6 5" />
                                                    <path d="M17 17H3s3-2 3-9a4.67 4.67 0 0 1 .3-1.7" />
                                                    <path d="M10.3 21a1.94 1.94 0 0 0 3.4 0" />
                                                    <path d="m2 2 20 20" />
                                                </svg>
                                            )
                                        }
                                        
                                    </button>
                                </div>
                            </Tooltip.Trigger>
                            <Tooltip.Portal>
                                <Tooltip.Content className="z-50 overflow-hidden rounded-md backdrop-blur-xl border bg-popover px-3 py-1.5 text-sm text-popover-foreground shadow-md animate-in fade-in-0 zoom-in-95 data-[state=closed]:animate-out data-[state=closed]:fade-out-0 data-[state=closed]:zoom-out-95 data-[side=bottom]:slide-in-from-top-2 data-[side=left]:slide-in-from-right-2 data-[side=right]:slide-in-from-left-2 data-[side=top]:slide-in-from-bottom-2" sideOffset={5}>
                                    Notification Sound: {notificationsSound ? "Enabled" : "Disabled"}
                                </Tooltip.Content>
                            </Tooltip.Portal>
                        </Tooltip.Root>
                    </Tooltip.Provider>


                </div>
                <div className="flex flex-col space-y-4">
                    <div className="flex space-x-2">
                        <Select.Root value={matchesFilter} onValueChange={handleMatchesFilterChange} >
                            <Select.Trigger className="flex h-10 items-center justify-between rounded-md border border-input bg-background px-3 py-2 text-sm ring-offset-background placeholder:text-muted-foreground focus:outline-none focus:ring-2 focus:ring-ring focus:ring-offset-2 disabled:cursor-not-allowed disabled:opacity-50 [&>span]:line-clamp-1 w-full sm:w-1/4">
                                <Select.Value>{matchesSelectEnuem[matchesFilter]}</Select.Value>
                                <Select.Icon className="lucide lucide-chevron-down h-4 w-4 opacity-50">
                                    <ChevronDownIcon />
                                </Select.Icon>
                            </Select.Trigger>
                            <Select.Portal>
                                <Select.Content position="popper" side="bottom" className="relative z-50 max-h-96 min-w-[8rem] overflow-hidden rounded-md border bg-popover/75 backdrop-blur-md text-popover-foreground shadow-md data-[state=open]:animate-in data-[state=closed]:animate-out data-[state=closed]:fade-out-0 data-[state=open]:fade-in-0 data-[state=closed]:zoom-out-95 data-[state=open]:zoom-in-95 data-[side=bottom]:slide-in-from-top-2 data-[side=left]:slide-in-from-right-2 data-[side=right]:slide-in-from-left-2 data-[side=top]:slide-in-from-bottom-2 data-[side=bottom]:translate-y-1 data-[side=left]:-translate-x-1 data-[side=right]:translate-x-1 data-[side=top]:-translate-y-1">
                                    <Select.Viewport className="p-1 h-[var(--radix-select-trigger-height)] w-full min-w-[var(--radix-select-trigger-width)]">
                                        <Select.Group>
                                            <Select.Item value="all" className="relative flex w-full cursor-default select-none items-center rounded-sm py-1.5 pl-8 pr-2 text-sm outline-none focus:bg-accent focus:text-accent-foreground data-[disabled]:pointer-events-none data-[disabled]:opacity-50">
                                                {matchesFilter === "all" && <SelectedCheckSVG/>}
                                                All matches
                                            </Select.Item>
                                            <Select.Item value="premier" className="relative flex w-full cursor-default select-none items-center rounded-sm py-1.5 pl-8 pr-2 text-sm outline-none focus:bg-accent focus:text-accent-foreground data-[disabled]:pointer-events-none data-[disabled]:opacity-50">
                                                {matchesFilter === "premier" && <SelectedCheckSVG/>}
                                                Premier
                                            </Select.Item>
                                            <Select.Item value="competitive" className="relative flex w-full cursor-default select-none items-center rounded-sm py-1.5 pl-8 pr-2 text-sm outline-none focus:bg-accent focus:text-accent-foreground data-[disabled]:pointer-events-none data-[disabled]:opacity-50">
                                                {matchesFilter === "competitive" && <SelectedCheckSVG/>}
                                                Competitive
                                            </Select.Item>
                                            <Select.Item value="wingman" className="relative flex w-full cursor-default select-none items-center rounded-sm py-1.5 pl-8 pr-2 text-sm outline-none focus:bg-accent focus:text-accent-foreground data-[disabled]:pointer-events-none data-[disabled]:opacity-50">
                                                {matchesFilter === "wingman" && <SelectedCheckSVG/>}
                                                Wingman
                                            </Select.Item>
                                        </Select.Group>
                                    </Select.Viewport>

                                </Select.Content>
                            </Select.Portal>
                        </Select.Root>

                        <Select.Root value={cheatFilter} onValueChange={handleCheatFilterChange} >
                            <Select.Trigger className="flex h-10 items-center justify-between rounded-md border border-input bg-background px-3 py-2 text-sm ring-offset-background placeholder:text-muted-foreground focus:outline-none focus:ring-2 focus:ring-ring focus:ring-offset-2 disabled:cursor-not-allowed disabled:opacity-50 [&>span]:line-clamp-1 w-full sm:w-1/4">
                                <Select.Value>
                                    <span style={{pointerEvents: 'none'}}>
                                        <div className="w-full flex items-center space-x-2">
                                            {
                                                cheatFilter !== "all" ? (
                                                    cheatFilter === "other" ? (
                                                        <svg stroke="currentColor" fill="currentColor" strokeWidth={0} viewBox="0 0 320 512" className="w-7 h-7 p-1" height="1em" width="1em" xmlns="http://www.w3.org/2000/svg">
                                                            <path d="M80 160c0-35.3 28.7-64 64-64h32c35.3 0 64 28.7 64 64v3.6c0 21.8-11.1 42.1-29.4 53.8l-42.2 27.1c-25.2 16.2-40.4 44.1-40.4 74V320c0 17.7 14.3 32 32 32s32-14.3 32-32v-1.4c0-8.2 4.2-15.8 11-20.2l42.2-27.1c36.6-23.6 58.8-64.1 58.8-107.7V160c0-70.7-57.3-128-128-128H144C73.3 32 16 89.3 16 160c0 17.7 14.3 32 32 32s32-14.3 32-32zm80 320a40 40 0 1 0 0-80 40 40 0 1 0 0 80z" />
                                                        </svg>
                                                    ) : (
                                                        <CheatLogo cheat={cheatFilter} />
                                                    )
                                                    
                                                ) : (false)
                                            }
                                        
                                            <span>{cheatsSelectEnuem[cheatFilter]}</span>
                                        </div>
                                    </span>
                                </Select.Value>
                                <Select.Icon className="lucide lucide-chevron-down h-4 w-4 opacity-50">
                                    <ChevronDownIcon />
                                </Select.Icon>
                            </Select.Trigger>
                            <Select.Portal>
                                <Select.Content position="popper" side="bottom" className="relative z-50 max-h-96 min-w-[8rem] overflow-hidden rounded-md border bg-popover/75 backdrop-blur-md text-popover-foreground shadow-md data-[state=open]:animate-in data-[state=closed]:animate-out data-[state=closed]:fade-out-0 data-[state=open]:fade-in-0 data-[state=closed]:zoom-out-95 data-[state=open]:zoom-in-95 data-[side=bottom]:slide-in-from-top-2 data-[side=left]:slide-in-from-right-2 data-[side=right]:slide-in-from-left-2 data-[side=top]:slide-in-from-bottom-2 data-[side=bottom]:translate-y-1 data-[side=left]:-translate-x-1 data-[side=right]:translate-x-1 data-[side=top]:-translate-y-1">
                                    <Select.Viewport className="p-1 h-[var(--radix-select-trigger-height)] w-full min-w-[var(--radix-select-trigger-width)]">
                                        <Select.Group>
                                            <Select.Item value="all" className="relative flex w-full cursor-default select-none items-center rounded-sm py-1.5 pl-8 pr-2 text-sm outline-none focus:bg-accent focus:text-accent-foreground data-[disabled]:pointer-events-none data-[disabled]:opacity-50">
                                                {cheatFilter === "all" && <SelectedCheckSVG/>}
                                                All cheats
                                            </Select.Item>
                                            <Select.Item value="gamesense" className="relative flex w-full cursor-default select-none items-center rounded-sm py-1.5 pl-8 pr-2 text-sm outline-none focus:bg-accent focus:text-accent-foreground data-[disabled]:pointer-events-none data-[disabled]:opacity-50">
                                                {cheatFilter === "gamesense" && <SelectedCheckSVG/>}
                                                <div className="w-full flex items-center space-x-2">
                                                    <CheatLogo cheat="gamesense" />
                                                    <span>GameSense</span>
                                                </div>
                                            </Select.Item>
                                            <Select.Item value="neverlose" className="relative flex w-full cursor-default select-none items-center rounded-sm py-1.5 pl-8 pr-2 text-sm outline-none focus:bg-accent focus:text-accent-foreground data-[disabled]:pointer-events-none data-[disabled]:opacity-50">
                                                {cheatFilter === "neverlose" && <SelectedCheckSVG/>}
                                                <div className="w-full flex items-center space-x-2">
                                                    <CheatLogo cheat="neverlose" />
                                                    <span>NeverLose</span>
                                                </div>
                                            </Select.Item>
                                            <Select.Item value="fatality" className="relative flex w-full cursor-default select-none items-center rounded-sm py-1.5 pl-8 pr-2 text-sm outline-none focus:bg-accent focus:text-accent-foreground data-[disabled]:pointer-events-none data-[disabled]:opacity-50">
                                                {cheatFilter === "fatality" && <SelectedCheckSVG/>}
                                                <div className="w-full flex items-center space-x-2">
                                                    <CheatLogo cheat="fatality" />
                                                    <span>Fatality</span>
                                                </div>
                                            </Select.Item>
                                            <Select.Item value="aimware" className="relative flex w-full cursor-default select-none items-center rounded-sm py-1.5 pl-8 pr-2 text-sm outline-none focus:bg-accent focus:text-accent-foreground data-[disabled]:pointer-events-none data-[disabled]:opacity-50">
                                                {cheatFilter === "aimware" && <SelectedCheckSVG/>}
                                                <div className="w-full flex items-center space-x-2">
                                                    <CheatLogo cheat="aimware" />
                                                    <span>Aimware</span>
                                                </div>
                                            </Select.Item>
                                            <Select.Item value="obstruction" className="relative flex w-full cursor-default select-none items-center rounded-sm py-1.5 pl-8 pr-2 text-sm outline-none focus:bg-accent focus:text-accent-foreground data-[disabled]:pointer-events-none data-[disabled]:opacity-50">
                                                {cheatFilter === "obstruction" && <SelectedCheckSVG/>}
                                                <div className="w-full flex items-center space-x-2">
                                                    <CheatLogo cheat="obstruction" />
                                                    <span>Obstruction</span>
                                                </div>
                                            </Select.Item>
                                            <Select.Item value="other" className="relative flex w-full cursor-default select-none items-center rounded-sm py-1.5 pl-8 pr-2 text-sm outline-none focus:bg-accent focus:text-accent-foreground data-[disabled]:pointer-events-none data-[disabled]:opacity-50">
                                                {cheatFilter === "other" && <SelectedCheckSVG/>}
                                                <div className="w-full flex items-center space-x-2">
                                                    <svg stroke="currentColor" fill="currentColor" strokeWidth={0} viewBox="0 0 320 512" className="w-7 h-7 p-1" height="1em" width="1em" xmlns="http://www.w3.org/2000/svg">
                                                        <path d="M80 160c0-35.3 28.7-64 64-64h32c35.3 0 64 28.7 64 64v3.6c0 21.8-11.1 42.1-29.4 53.8l-42.2 27.1c-25.2 16.2-40.4 44.1-40.4 74V320c0 17.7 14.3 32 32 32s32-14.3 32-32v-1.4c0-8.2 4.2-15.8 11-20.2l42.2-27.1c36.6-23.6 58.8-64.1 58.8-107.7V160c0-70.7-57.3-128-128-128H144C73.3 32 16 89.3 16 160c0 17.7 14.3 32 32 32s32-14.3 32-32zm80 320a40 40 0 1 0 0-80 40 40 0 1 0 0 80z" />
                                                    </svg>
                                                    <span>Other</span>
                                                </div>
                                            </Select.Item>
                                            
                                        </Select.Group>
                                    </Select.Viewport>

                                </Select.Content>
                            </Select.Portal>
                        </Select.Root>

                        <input
                            className="flex h-10 rounded-md border border-input bg-background px-3 py-2 text-sm ring-offset-background file:border-0 file:bg-transparent file:text-sm file:font-medium placeholder:text-muted-foreground focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring focus-visible:ring-offset-2 disabled:cursor-not-allowed disabled:opacity-50 w-full"
                            placeholder="Your ELO"
                            min={0}
                            max={35000}
                            step={1000}
                            type="number"
                            value={minEloFilterValue}
                            onChange={handleMinEloChange}
                        />
                        
                        <Select.Root value={lobbyFilter} onValueChange={handleLobbyFilterChange} >
                            <Select.Trigger className="flex h-10 items-center justify-between rounded-md border border-input bg-background px-3 py-2 text-sm ring-offset-background placeholder:text-muted-foreground focus:outline-none focus:ring-2 focus:ring-ring focus:ring-offset-2 disabled:cursor-not-allowed disabled:opacity-50 [&>span]:line-clamp-1 w-full sm:w-1/4">
                                <Select.Value>{lobbiesEnuem[lobbyFilter]}</Select.Value>
                                <Select.Icon className="lucide lucide-chevron-down h-4 w-4 opacity-50">
                                    <ChevronDownIcon />
                                </Select.Icon>
                            </Select.Trigger>
                            <Select.Portal>
                                <Select.Content position="popper" side="bottom" className="relative z-50 max-h-96 min-w-[8rem] overflow-hidden rounded-md border bg-popover/75 backdrop-blur-md text-popover-foreground shadow-md data-[state=open]:animate-in data-[state=closed]:animate-out data-[state=closed]:fade-out-0 data-[state=open]:fade-in-0 data-[state=closed]:zoom-out-95 data-[state=open]:zoom-in-95 data-[side=bottom]:slide-in-from-top-2 data-[side=left]:slide-in-from-right-2 data-[side=right]:slide-in-from-left-2 data-[side=top]:slide-in-from-bottom-2 data-[side=bottom]:translate-y-1 data-[side=left]:-translate-x-1 data-[side=right]:translate-x-1 data-[side=top]:-translate-y-1">
                                    <Select.Viewport className="p-1 h-[var(--radix-select-trigger-height)] w-full min-w-[var(--radix-select-trigger-width)]">
                                        <Select.Group>
                                            <Select.Item value="all" className="relative flex w-full cursor-default select-none items-center rounded-sm py-1.5 pl-8 pr-2 text-sm outline-none focus:bg-accent focus:text-accent-foreground data-[disabled]:pointer-events-none data-[disabled]:opacity-50">
                                                {lobbyFilter === "all" && <SelectedCheckSVG/>}
                                                All lobbies
                                            </Select.Item>
                                            <Select.Item value="legit" className="relative flex w-full cursor-default select-none items-center rounded-sm py-1.5 pl-8 pr-2 text-sm outline-none focus:bg-accent focus:text-accent-foreground data-[disabled]:pointer-events-none data-[disabled]:opacity-50">
                                                {lobbyFilter === "legit" && <SelectedCheckSVG/>}
                                                Legit
                                            </Select.Item>
                                            <Select.Item value="semi" className="relative flex w-full cursor-default select-none items-center rounded-sm py-1.5 pl-8 pr-2 text-sm outline-none focus:bg-accent focus:text-accent-foreground data-[disabled]:pointer-events-none data-[disabled]:opacity-50">
                                                {lobbyFilter === "semi" && <SelectedCheckSVG/>}
                                                Semi-Rage
                                            </Select.Item>
                                            <Select.Item value="rage" className="relative flex w-full cursor-default select-none items-center rounded-sm py-1.5 pl-8 pr-2 text-sm outline-none focus:bg-accent focus:text-accent-foreground data-[disabled]:pointer-events-none data-[disabled]:opacity-50">
                                                {lobbyFilter === "rage" && <SelectedCheckSVG/>}
                                                Rage / HvH
                                            </Select.Item>
                                            <Select.Item value="grief" className="relative flex w-full cursor-default select-none items-center rounded-sm py-1.5 pl-8 pr-2 text-sm outline-none focus:bg-accent focus:text-accent-foreground data-[disabled]:pointer-events-none data-[disabled]:opacity-50">
                                                {lobbyFilter === "grief" && <SelectedCheckSVG/>}
                                                Griefing
                                            </Select.Item>
                                        </Select.Group>
                                    </Select.Viewport>

                                </Select.Content>
                            </Select.Portal>
                        </Select.Root>

                        <AddCodeDialogue setCodesList={setCodesList}/>

                    </div>
                    <div className="rounded-lg bg-card text-card-foreground shadow-sm space-y-8 dark:bg-black/25 border">
                        <div className="relative w-full overflow-auto">
                            <table className="w-full caption-bottom text-sm">
                                <thead className="[&_tr]:border-b">
                                    <tr className="border-b transition-colors hover:bg-muted/50 data-[state=selected]:bg-muted">
                                        <th className="h-12 px-4 text-left align-middle font-medium text-muted-foreground [&:has([role=checkbox])]:pr-0">Type</th>
                                        <th className="h-12 px-4 text-left align-middle font-medium text-muted-foreground [&:has([role=checkbox])]:pr-0">Region</th>
                                        <th className="h-12 px-4 text-left align-middle font-medium text-muted-foreground [&:has([role=checkbox])]:pr-0">Player</th>
                                        <th className="h-12 px-4 text-left align-middle font-medium text-muted-foreground [&:has([role=checkbox])]:pr-0">Details</th>
                                        <th className="h-12 px-4 text-left align-middle font-medium text-muted-foreground [&:has([role=checkbox])]:pr-0 pl-2">Min. ELO</th>
                                        <th className="h-12 px-4 text-left align-middle font-medium text-muted-foreground [&:has([role=checkbox])]:pr-0">Time</th>
                                        <th className="h-12 px-4 text-left align-middle font-medium text-muted-foreground [&:has([role=checkbox])]:pr-0" />
                                    </tr>
                                </thead>
                                <tbody className="[&_tr:last-child]:border-0" >
                                    {
                                        codesList.map( (code, i) => {
                                            if( matchesFilter !== "all" ) {
                                                if( matchesFilter !== code.type ) return false
                                            }
                                            if( cheatFilter !== "all" ) {
                                                if( cheatFilter !== code.cheat ) return false
                                            }
                                            if( lobbyFilter !== "all" ) {
                                                if( lobbyFilter !== code.lobby ) return false
                                            }
                                            if( minEloFilter !== "" ) {
                                                if( Number(minEloFilter) > code.minELO + 10000 ) {
                                                    return false
                                                } else if ( Number(minEloFilter) < code.minELO - 10000 ) {
                                                    return false
                                                }
                                            }
                                            return <CodeItem key={i} code={code} />
                                        })
                                    }
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </>
}

const SelectedCheckSVG = () => {
    return <>
        <span className="absolute left-2 flex h-3.5 w-3.5 items-center justify-center">
            <span aria-hidden="true">
                <svg xmlns="http://www.w3.org/2000/svg" width={24} height={24} viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth={2} strokeLinecap="round" strokeLinejoin="round" className="lucide lucide-check h-4 w-4">
                    <path d="M20 6 9 17l-5-5" />
                </svg>
            </span>
        </span>
    </>
}

const CheatLogo = ({cheat}) => (
    <img
        alt={cheatsSelectEnuem[cheat]}
        loading="lazy"
        width={28}
        height={28}
        decoding="async"
        data-nimg={1}
        className="rounded-lg border w-7 h-7"
        src={`./static/${cheat}.png`}
        style={{color: 'transparent'}}
    />

)

export default Matchmaking