const NetworkFlags = ({flag}) => {
    return <>
        { flag === "eu" && <EuropeSVG/> }
        { flag === "us" && <UsSVG/> }
        { flag === "ca" && <MontrealSVG/> }
        { flag === "gb" && <LondonSVG/> }
        { flag === "fr" && <ParisSVG/> }
        { flag === "de" && <FrankfurtSVG/> }
        { flag === "se" && <StockholmSVG/> }
        { flag === "sg" && <SingaporeSVG/> }
        { flag === "au" && <MelbourneSVG/> }

        { flag === "jp" && <TokyoSVG/> }
        { flag === "br" && <PauloSVG/> }
        { flag === "ae" && <DubaiSVG/> }
        { flag === "ch" && <ZurichSVG/> }
        { flag === "pl" && <PolandSVG/> }
        { flag === "in" && <DelhiSVG/> }
        { flag === "mx" && <QueretaroSVG/> }

    </>
}

export default NetworkFlags