import { useEffect, useState } from "react"
import { Link } from "react-router-dom"

const reminderMessages = [
    "Remember, it's not camping, it's strategic leisure!",
    "The only thing more toxic than the chat is the neon-colored skins.",
    "The only thing louder than the sound of gunfire is the sound of egos being shattered.",
    "It's not just about hitting shots; it's about hitting shots that make your opponents ragequit.",
    "The ultimate test of skill and how quickly you can blame your cheat for missing that shot.",
    "Every headshot is a love letter to your enemy's forehead.", "The only thing more fragile than your ego is your hitbox.",
    "A never-ending saga of salt and suspicion.", "The trash talk is more entertaining than the actual gameplay.",
    "Missing shots is just a prelude to the real entertainment.",
    "The kill feed isn't just for kills, it's for feeding inflated egos."
]

const TopBar = () => {
    const [reminderHidden, setReminderHidden] = useState(false)
    const [reminderMessage, setReminderMessage] = useState(0)
    const [dashoffset, setDashoffset] = useState(0)

    useEffect( () => {
        setDashoffset(0)
        let int = setInterval(()=>{
            setDashoffset( oldOffset => oldOffset >= 100 ? (
                clearInterval(int),
            100) : oldOffset + 1)
        }, 1e3 * 10 / 100)

        return ()=>clearInterval(int)
    }, [reminderMessage] )

    useEffect( () => {
        let int = setInterval(()=>{
            setReminderHidden(true)

            setTimeout(()=>{
                setReminderMessage( oldMessage =>{
                    let newMessage
                    do
                        newMessage = Math.floor(Math.random() * reminderMessages.length)
                    while (newMessage === oldMessage) 
                    
                    return newMessage
                })

                setReminderHidden(false)
            }, 250)
        }, 10000)

        return () => clearInterval(int)
    }, [] )

    return <>
        <div className="bg-white dark:bg-black/40 border-b border-b-border/50 dots">
            <div className="flex items-center justify-between bg-transparent py-4 px-4 3xl:px-0 mx-auto max-w-screen-2xl">
                <div className="flex items-center space-x-4 text-2xl">
                    <Link to="/" className="transition hover:brightness-75">
                        <div className="p-2 bg-foreground rounded-lg bg-gradient-to-br from-[#6969ff] to-[#8989ff]">
                            <svg stroke="currentColor" fill="none" strokeWidth={2} viewBox="0 0 24 24" strokeLinecap="round" strokeLinejoin="round" className="text-white" height="1em" width="1em" xmlns="http://www.w3.org/2000/svg">
                                <line x1={6} x2={10} y1={11} y2={11} />
                                <line x1={8} x2={8} y1={9} y2={13} />
                                <line x1={15} x2="15.01" y1={12} y2={12} />
                                <line x1={18} x2="18.01" y1={10} y2={10} />
                                <path d="M17.32 5H6.68a4 4 0 0 0-3.978 3.59c-.006.052-.01.101-.017.152C2.604 9.416 2 14.456 2 16a3 3 0 0 0 3 3c1 0 1.5-.5 2-1l1.414-1.414A2 2 0 0 1 9.828 16h4.344a2 2 0 0 1 1.414.586L17 18c.5.5 1 1 2 1a3 3 0 0 0 3-3c0-1.545-.604-6.584-.685-7.258-.007-.05-.011-.1-.017-.151A4 4 0 0 0 17.32 5z" />
                            </svg>
                        </div>
                    </Link>
                    <div className="flex flex-col justify-center p-2">
                        <Link to="/" className="transition hover:brightness-75 no-underline">
                            <h1 className="flex space-x-2 items-center font-semibold dark:text-white">
                                <span>CS Server Browser</span>
                                <div className="items-center rounded-full border px-2.5 py-0.5 text-xs font-semibold transition-colors focus:outline-none focus:ring-2 focus:ring-ring focus:ring-offset-2 text-foreground hidden md:flex">
                                    <div className="w-1 h-1 mr-2 rounded-full bg-affirmative animate-pulse" />84 visitors
                                </div>
                            </h1>
                        </Link>
                        <div className="flex items-center pr-4">
                            <div className={`flex space-x-1 items-center text-xs transition-opacity duration-250 ${reminderHidden ? 'opacity-0' : 'opacity-100'}`}>
                                <div className="inline-flex relative w-3 h-3">
                                    <svg className="absolute top-0 left-0 w-full h-full" viewBox="0 0 100 100">
                                        <circle className="text-border stroke-current" strokeWidth={15} fill="transparent" r="37.5" cx={50} cy={50} transform="rotate(-90 50 50)" />
                                        <circle className="text-primary stroke-current transition-all duration-0" strokeWidth={15} strokeDasharray="235.61944901923448" strokeDashoffset={(100 - dashoffset) / 100 * 235.61944901923448} /*strokeDashoffset="0"*/ fill="transparent" r="37.5" cx={50} cy={50} transform="rotate(-90 50 50)" />
                                    </svg>
                                </div>
                                <span>{reminderMessages[reminderMessage]}</span>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="flex items-center space-x-4">

                    <Link to="https://discord.gg/premade">
                        <button className="inline-flex items-center justify-center whitespace-nowrap rounded-md text-sm font-medium ring-offset-background transition-colors focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring focus-visible:ring-offset-2 disabled:pointer-events-none disabled:opacity-50 border border-input bg-background hover:bg-primary hover:text-primary-foreground h-10 px-4 py-2 text-foreground">
                            <svg stroke="currentColor" fill="currentColor" strokeWidth={0} viewBox="0 0 640 512" className="text-lg" height="1em" width="1em" xmlns="http://www.w3.org/2000/svg">
                                <path d="M524.531,69.836a1.5,1.5,0,0,0-.764-.7A485.065,485.065,0,0,0,404.081,32.03a1.816,1.816,0,0,0-1.923.91,337.461,337.461,0,0,0-14.9,30.6,447.848,447.848,0,0,0-134.426,0,309.541,309.541,0,0,0-15.135-30.6,1.89,1.89,0,0,0-1.924-.91A483.689,483.689,0,0,0,116.085,69.137a1.712,1.712,0,0,0-.788.676C39.068,183.651,18.186,294.69,28.43,404.354a2.016,2.016,0,0,0,.765,1.375A487.666,487.666,0,0,0,176.02,479.918a1.9,1.9,0,0,0,2.063-.676A348.2,348.2,0,0,0,208.12,430.4a1.86,1.86,0,0,0-1.019-2.588,321.173,321.173,0,0,1-45.868-21.853,1.885,1.885,0,0,1-.185-3.126c3.082-2.309,6.166-4.711,9.109-7.137a1.819,1.819,0,0,1,1.9-.256c96.229,43.917,200.41,43.917,295.5,0a1.812,1.812,0,0,1,1.924.233c2.944,2.426,6.027,4.851,9.132,7.16a1.884,1.884,0,0,1-.162,3.126,301.407,301.407,0,0,1-45.89,21.83,1.875,1.875,0,0,0-1,2.611,391.055,391.055,0,0,0,30.014,48.815,1.864,1.864,0,0,0,2.063.7A486.048,486.048,0,0,0,610.7,405.729a1.882,1.882,0,0,0,.765-1.352C623.729,277.594,590.933,167.465,524.531,69.836ZM222.491,337.58c-28.972,0-52.844-26.587-52.844-59.239S193.056,219.1,222.491,219.1c29.665,0,53.306,26.82,52.843,59.239C275.334,310.993,251.924,337.58,222.491,337.58Zm195.38,0c-28.971,0-52.843-26.587-52.843-59.239S388.437,219.1,417.871,219.1c29.667,0,53.307,26.82,52.844,59.239C470.715,310.993,447.538,337.58,417.871,337.58Z" />
                            </svg>
                        </button>
                    </Link>
                </div>
            </div>
        </div>
    </>
}

export default TopBar